import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
     state:{
          commentOrder_no:null,
     //     订单详情页的订单号
          order_no:null,
     //     再来一单的订单号
          again_order:null,
     //     设置拼团期号
          period_id:null,
          //品提问到详情页的提问内容
          askContent:null,
     //     商品提问的创建时间
          AskCreatTime:null,
     //     全局抽奖拼团提醒
          allMessageList:[],
     //     图片预览的数组
          lookImgArr:null,
     //     图片预览的初始位置
          imgIndex:null,
          //游戏全局提醒的数组
          gameMessageList:[],
     //     店铺信息
          shopInfo:null,
     //     首页的滚动高度
          homePageScoHeight:null,
     //     设置进入抽奖详情的信息,
          drawInfo:null,
          address:null,
     //     商品详情滚动高度
          goodDetailScrool:300,
     //     设置店铺点击买家秀显示的详情
          shopBuyShow:null,
          //到订单确认页面的信息
          entDetailSkuInfo:null,
          //是加入购物车还是购买的判断
          isCarOrBuy:true,
          // 传递到sku的数据
          skuInfo:null,
     //     从拼团成功订单详情，到拼单成功页面的数据
          groupSuccess:[],
     //     设置售后数据
          afterInfo:null,
     //     设置订单收货时间
          afterOrderInfo:null,
     //     设置售后详情数据在添加物流信息
          afterInfoAddress:null,
     //     设置物流公司
          express:null,
     //     设置售后订单号
          after_order_yun:null,
     //     设置从订单详情到发票详情的订单信息
          orderBillInfo:null,
     //     购物车预下单数据
          shoppingCartAddOrder:null
     },
     mutations : {
          SetshoppingCartAddOrder(state,val){
               state.shoppingCartAddOrder = val
          },
          setOrderBillInfo(state,val){
               state.orderBillInfo = val
          },
          setAfterInfo(state,val){
               state.afterInfo = val
          },
          set_ask_goods_info(state,val){
               state.ask_goods_info = val
          },
          set_after_order_yun(state,val){
               state.after_order_yun = val
          },
          setExpress(state,val){
               state.express = val
          },
          setAfterDetail(state,val){
               state.afterInfoAddress = val
          },
          setAfterOrderInfo(state,val){
               state.afterOrderInfo = val
          },
          //设置传递到sku的数据
          setSkuInfo(state,item){
               state.skuInfo = item
          },
          //判断是加入购物车还是购买
          setIsCarOrBuy(state,item){
               state.isCarOrBuy = item
          },
          //设置到订单确认页面的信息
          setEntDetailSkuInfo(state,item){
               state.entDetailSkuInfo = item
          },
          //     设置店铺点击买家秀显示的详情
          setShopBuyShow(state,item){
               state.shopBuyShow = item
          },
          //设置上平详情的滚动高度
          setgdscrool(state,item){
               state.goodDetailScrool = item
          },
          //设置选中的地址
          setAddress(state,item){
               state.address = item
          },
          //     设置进入抽奖详情的信息,
          setDrawInfo(state,item){
               state.drawInfo = item
          },
          //设置首页的滚动高度
          setHomeScoHeight(state,item){
            state.homePageScoHeight = item
          },
          //清除设置视屏中的商品购买列表
          clearVideoComList(state){

          },
          //设置店铺信息
          getShopInfo(state,item){
               state.shopInfo = item
          },
          //设置游戏全网通知
          getGameMess(state,item){
               state.gameMessageList.push(item)
          },
          //设置图片预览赋值与初始的index
          setImgArr(state,obj){
               state.lookImgArr = obj.imgArr
               state.imgIndex = obj.imgIndex
          },
          //设置全局抽奖拼团提醒
          getAllmess(state,item){
               state.allMessageList.push(item)
          },
          //     设置商品提问到详情页的提问内容
          setAskContent(state,item){
               state.askContent = item
          },
          //设置拼团期号
          setPeriod(state,item){
               state.period_id = item
          },
     //     设置评价的订单值
          setCommentOrder(state,item){
               state.commentOrder_no = item
          },
     //     设置详情页的订单号
          setOrderNo(state,item){
               state.order_no = item
          },
     //     设置再来一单的订单号
          setagainOrder(state,item){
               state.again_order = item
          },
          setGroupSuccess(state,item){
               state.groupSuccess = item
          },
     },
     actions:{
          SetshoppingCartAddOrder({commit},val){
               commit("SetshoppingCartAddOrder",val)
          },
          setOrderBillInfo({commit},val){
               commit("setOrderBillInfo",val)
          },
          set_after_order_yun({commit},val){
               commit("set_after_order_yun",val)
          },
          setExpress({commit},val){
               commit("setExpress",val)
          },
          setAfterDetail({commit},val){
               commit("setAfterDetail",val)
          },
          setAfterOrderInfo({commit},val){
               commit("setAfterOrderInfo",val)
          },
          setGroupSuccess({commit},val){
               commit("setGroupSuccess",val)
          },
          setAfterInfo({commit},val){
               commit("setAfterInfo",val)
          }
     }
})


export default store



