          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
     }
     .cominfo-content{
          font-size: 1.3rem;
          overflow: hidden;
          margin-top: 1.5rem;
          position: relative;
     }
     .shopInfo{
          display: flex;
          justify-content: space-between;
          font-size: 1.5rem;
