     }
     .comInfo{
          padding: 1.5rem 1rem;
          background-color: #ffffff;
          margin-top: 7px;
     }
     .address > div{
          margin-left: 1.3rem;
          font-size: 1.3rem;

     }
     .address{
          height: 6.4rem;
          width: 100%;
          display: flex;
          justify-content: flex-start;
          align-items: center;
          padding-left: 1.2rem;
          background-color: #ffffff;
          img{
               width: 1.5rem;
               height: 1.85rem;
          }
     }
     .banner-type span{
          position: relative;
          top: 0.5rem;
          color: #ffffff;
          font-size: 1.1rem;
     }
     .banner-type h4{
          font-size: 1.7rem;
          color: #ffffff;
          font-weight: 700;
          position: relative;
     }
     .banner-type{
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          height: 7.1rem;
          background-image: url("~@/common/image/orderEetailsImg/di.png");
          background-size: 100% 100%;
     }
     .orderdetails-conent{
          height: 100vh;
          overflow-y: scroll;
          background-color: #f6f6f6;
          padding-top: 4.4rem;
     }
     .gujia{
          width: 100vw;
          height: 100vh;
          top: 0;
          left: 0;
          position: fixed;
          z-index: 100;
          img{
               width: 100%;
               height: 100%;
          }
     }
     .zhanewi_back{
          width: 4rem;
          height: 4rem;
          position: absolute;
          left: 1.1rem;
          top: 20px;
          img{
               width: 0.85rem;
               height: 1.65rem;
          }
     }
     .order_name_phone{
          span{
               font-weight: 600;
          }
     }
     .message_shop{
          img{
               width: 1.5rem;
               height: 1.5rem;
          }
          span{
               margin-left: 5px;
          }
     }
     .goods_title{
          width: 19rem;
          float: left;
          margin-left: 15px;
          p{
               span{
                    border: solid 1px #f55056;
                    font-size: 0.8rem;
                    position: relative;
                    top: -0.2rem;
                    color: #f55056;
               }
          }
          >span{
               text-overflow: -o-ellipsis-lastline;
               overflow: hidden;
               text-overflow: ellipsis;
               display: -webkit-box;
               -webkit-line-clamp: 2;
               line-clamp: 2;
               -webkit-box-orient: vertical;
               padding-right: 28px;

          }
     }
     .goods_right_price{
          float: right;
          display: flex;
          flex-direction: column;
          text-align: right;
          .goods_price_shop{
               color: #f55056;
               span{
                    font-family: 数字专用;
               }
          }
     }
     .border_radius_bottom{
          border-bottom-left-radius: 7px;
          border-bottom-right-radius: 7px;
     }

     .border_radius_top{
          border-top-left-radius: 7px;
          border-top-right-radius: 7px;
     }
     .all_price{
     }
     .copy{
          display: inline-block;
          width: 4rem;
          height: 1.7rem;
          font-size: 1.1rem;
          color: #9d9d9d;
          border: solid 1px #9d9d9d;
          border-radius: 0.2rem;
          text-align: center;
          margin-left: 1rem;
          border-radius: 0.85rem;
     }
     .pay_btn{
          padding: 0 22px !important;
     }
     .apply_after{
          display: inline-block;
          line-height: 2.9rem;
          border: none;
          color: #9d9d9d;
          border: solid 1px #9d9d9d;
          width: 8.9rem;
          text-align: center;
          position: absolute;
          bottom: 0;
          right: 0;
          border-radius: 1.5rem;
     }
     .waring_send{
          background-color: #f55056;
          width: 8.5rem;
          color: #ffffff;
          border: solid 1px #f55056;
          margin-left: 10px;
     }
     .order_one_ordprc{
          color: #f55056;
     }
     .shop_seded_banner{
          text-align: center;
     }
     .order_detail_header{
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          z-index: 20;
          background-color: #ffffff;
     }
     .shore_get{
          background-color: #f55056;
          color: #ffffff;
          border: solid 1px #f55056;
          width: 8.5rem;
          margin-left: 10px;
     }
     .looked_address{
          width: 8.5rem;
          color: #9d9d9d;
          border: solid 1px #9d9d9d;
          margin-right: 10px;
     }
     .comment_btn{
          color: #ffffff;
          width: 8.5rem;
          border: solid 1px #f55056;
          background-color: #f55056;
     }
     .banner-type-group{
          flex-direction: column;
          align-items: center;
     }
     .group_invent{
          width: 12.5rem !important;
          background-color: #f55056;
          color: #ffffff;
          display: inline-block;
          border: solid 1px #f55056;
          margin-left: 1rem;
     }
     .group_success_ceil{
          padding: 10px 12px;
          background-color:#ffffff;
          margin-top: 7px;
          display: flex;
          align-items: center;
          font-size: 1.3rem;
          justify-content: space-between;
          img{
               width: 0.7rem;
               height: 1.35rem;
          }
          .user_img{
               width: 2.5rem;
               height: 2.5rem;
               border-radius: 50%;
               background-size: cover;
               margin-right: 8px;
          }
          .group_success_ceil_right{
               display: flex;
               align-items: center;
               justify-content: flex-end;
          }
     }
     .lengthen{
          border: solid 1px #9d9d9d;
          color: #9d9d9d;
          width: 8.5rem;
     }
