          display: flex;
          align-items: center;
          img{
               width: 0.6rem;
               height: 1.1rem;
               margin-left: 1.3rem;
          }
     }
     .bill_ceil{
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 1.3rem;
     }
</style>

<template>
     <!--订单详情页-->
     <div class="orderdetails-conent steep_padding" v-if="order_info != null">
          <template v-if="order_info == null">
               <div class="gujia">
                    <div class="header">
                         <HeaderPage>
                              <span v-if="order_info.order_status == 0 || true">订单详情</span>
                              <!--<span v-if="order_info.order_status == 4">发表评价</span>-->
                         </HeaderPage>
                    </div>
                    <img src="@/common/image/orderEetailsImg/zhanwei.png" alt="">
               </div>
          </template>
          <template v-if="order_info != null">
              <div class="order_detail_content">
                   <van-popup v-model="show" position="bottom" :style="{ height: '60%' }">
                        <div>
                             <h3 style="text-align: center;font-size: 1.5rem;font-weight: 500;margin-bottom: 1rem;margin-top: 1.5rem">取消订单</h3>
                             <!--<span style="margin-left: 1rem;font-size: 14px">请选择取消的原因</span>-->
                             <van-radio-group v-model="radio">
                                  <van-cell-group>
                                       <van-cell title="我不想买了" clickable @click="radio = '1'">
                                            <van-radio slot="right-icon" name="1" checked-color="#e7204f" />
                                       </van-cell>
                                       <van-cell title="信息填写错误，重新拍" clickable @click="radio = '2'">
                                            <van-radio slot="right-icon" name="2" checked-color="#e7204f"  />
                                       </van-cell>
                                       <van-cell title="卖家缺货" clickable @click="radio = '3'">
                                            <van-radio slot="right-icon" name="3" checked-color="#e7204f" />
                                       </van-cell>
                                       <van-cell title="同城见面交易" clickable @click="radio = '4'">
                                            <van-radio slot="right-icon" name="4" checked-color="#e7204f" />
                                       </van-cell>
                                       <van-cell title="其他原因" clickable @click="radio = '5'">
                                            <van-radio slot="right-icon" name="5" checked-color="#e7204f" />
                                       </van-cell>
                                  </van-cell-group>
                             </van-radio-group>
                             <span style="width: 100%;background-color: #e65774;color: #ffffff;display: block;line-height: 40px;font-size: 14px;position: absolute;bottom: 0;display: flex;justify-content: space-between">
                         <span style="width: 50%;text-align: center;" @click="deleteOrder()">确定取消</span>
                         <span style="width: 50%;text-align: center;background-color: #e7204f" @click="show = false">暂不取消</span>
                    </span>
                        </div>
                   </van-popup >
                   <div class="order_detail_header">
                        <HeaderPage>
                             <span slot="headerTitle" v-if="order_info.order_status == 0 || true">订单详情</span>
                             <!--<span v-if="order_info.order_status == 4">发表评价</span>-->
                        </HeaderPage>
                   </div>
                   <template v-if="order_info.goods_types != 'goods_group'">
                        <div class="banner-type">
                             <template v-if="order_info.order_status == 0">
                                  <div>
                                       <h4>等待付款</h4>
                                       <span>剩余:{{ payOverTimeStr }}</span>
                                  </div>
                             </template>
                             <template v-if="order_info.order_status == 1">
                                  <h4>买家已付款，待发货</h4>
                             </template>
                             <template v-if="order_info.order_status == 3">
                                  <div class="shop_seded_banner">
                                       <h4>卖家已发货</h4>
                                       <span>还剩{{ time }}自动确认收货</span>
                                  </div>
                             </template>
                             <template v-if="order_info.order_status == 4">
                                  <h4>交易成功，待评价</h4>
                             </template>
                             <template v-if="order_info.order_status == 5">
                                  <h4>订单完成</h4>
                             </template>
                             <template v-if="order_info.order_status == 2">
                                  <h4>已取消</h4>
                             </template>
                        </div>
                   </template>
                   <!--拼团的信息-->
                   <template v-if="order_info.goods_types == 'goods_group'">
                        <div class="banner-type banner-type-group">
                             <template v-if="order_info.shipping_status == 0 && order_info.order_status == 1">
                                  <h4>拼团成功，待发货</h4>
                                  <span>预计拼单成功后24小时内发货</span>
                             </template>
                             <template v-if="order_info.shipping_status == -1 && order_info.order_status == 1">
                                  <h4>待分享</h4>
                                  <span>还差1人拼团成功</span>
                             </template>
                             <template v-if="order_info.order_status == 0">
                                  <h4>待付款</h4>
                             </template>
                             <template v-if="order_info.order_status == 3">
                                  <h4>拼团成功,已发货</h4>
                             </template>
                             <template v-if="order_info.order_status == 4">
                                  <h4>交易成功，待评价</h4>
                             </template>
                             <template v-if="order_info.order_status == 5">
                                  <h4>已完成</h4>
                             </template>
                               <template v-if="order_info.order_status == 2">
                                  <h4>已取消</h4>
                             </template>
                        </div>
                   </template>
                   <div @click="toLogisticsUrl" v-if="order_info.shipping_status == 1 && order_info.order_status == 3" class="logistics">
                        <!--<a :href=order_info.logistics_url>-->
                        <div>
                             <img src="@/common/image/orderEetailsImg/wuliu.png" style="width: 2.1rem;height: 1.7rem;"/>
                             <div class="logistics_text">
                                  <span style="font-weight: 600;color: #212121">{{ order_info.logistics_name }}</span>
                                  <span style="color: #888888;">{{ order_info.logistics_sn }}</span>
                             </div>
                        </div>
                        <img class="logistics_right" src="@/common/image/publicImg/huiRight.png" alt="">
                        <!--</a>-->
                   </div>
                   <van-divider />
                   <div class="address border_radius_bottom">
                        <img src="@/common/image/orderEetailsImg/dizhi.png"/>
                        <div>
                             <div class="order_name_phone" style=" margin-bottom: 0.5rem">
                                  <span>{{ order_info.receiver_name }}</span>
                                  <span class="phone">{{ order_info.receiver_phone.substr(0,3)+"****"+order_info.receiver_phone.substr(7)  }}</span>
                             </div>
                             <span>{{ order_info.receiver_address }}</span>
                        </div>
                   </div>
                   <div class="comInfo border_radius_top">
                        <div class="shopInfo">
                             <div @click="toShop">
                                  <span>{{ order_info.shop_name }}</span>
                                  <img src="@/common/image/orderEetailsImg/gengduo.png" style="width: 0.6rem;height: 1rem;"/>
                             </div>
                             <div class="message_shop" style="display: flex;align-items: center">
                                  <img src="@/common/image/orderEetailsImg/lianxishangjia.png"/>
                                  <span>联系商家 </span>
                             </div>
                        </div>
                        <div @click="toComDetail(item.goods_id)" class="cominfo-content" v-for="(item,index) in order_info.goods" :key="index + 'asik'">
                             <img :src=item.thumb alt="" style="width: 8.5rem;height: 8.5rem;float: left">
                             <div class="goods_title">
                                  <p>
                                       <span v-if="order_info.goods_types == 'goods_customs'">跨境进口</span>
                                       {{ item.title }}
                                  </p>
                                  <span style="color: #888888">{{ item.specs }}</span>
                             </div>
                             <div class="goods_right_price">
                              <span class="goods_price_shop">
                                   <span style="font-size: 1rem">￥</span><span style="font-size: 1.3rem">{{ item.goods_price.substring(0,item.goods_price.length - 3) }}</span><span style="font-size: 1rem">{{ item.goods_price.substring(item.goods_price.length - 3,item.goods_price.length) }}</span>
                              </span>
                                  <span style="font-size: 1.5rem;color: #888888">x{{ item.number }}</span>
                             </div>
                             <span @click.stop="aftersale(item)" class="apply_after" v-if="order_info.order_status >= 4 && item.refund_status == 0">申请售后</span>
                             <span @click.stop="findaftersale(item)" class="apply_after" v-if="order_info.order_status >= 4 && item.refund_status == 1">查看售后</span>
                        </div>
                   </div>
                   <div class="amount_price">
                        <div>
                             <span>商品总金额</span>
                             <span>￥{{ order_info.pay_price }}</span>
                        </div>
                        <div style="margin-top: 10px;">
                             <span>邮费</span>
                             <span>包邮</span>
                        </div>
                        <div style="margin-top: 10px;">
                             <span>活动优惠</span>
                             <span>-￥100</span>
                        </div>
                   </div>
                   <!--发票-->
                   <div v-if="order_info.order_status == 4 || order_info.order_status == 5" @click="toBillDetail" class="same_div bill_ceil">
                         <span style="color: #9d9d9d">发票类型</span>
                        <div class="bill_right">
                             <span>电子发票</span>
                             <img src="@/common/image/publicImg/mail_ico_point_v.png" alt="">
                        </div>
                   </div>
                   <div class="comNum border_radius_bottom">
                        <template v-if="order_info.order_status == 0">
                             <span>一共{{ order_info.goods.length }}件商品，待支付</span>
                             <span class="all_price;" style="color: #f55056">
                         <span class="num_family" style="font-size: 1.1rem">￥</span><span class="num_family">{{ order_info.pay_price.substring(0,order_info.pay_price.length - 3) }}</span><span class="num_family" style="font-size: 1.1rem">{{ order_info.pay_price.substring(order_info.pay_price.length - 3,order_info.pay_price.length) }}</span>
                         </span>
                        </template>
                        <template v-if="order_info.order_status == 1 || order_info.order_status == 3">
                             <span>实付款</span>
                             <span class="order_one_ordprc">
                              <span class="num_family" style="font-size: 1rem">￥</span><span class="num_family">{{ order_info.order_price.substring(0,order_info.order_price.length - 3) }}</span><span class="num_family" style="font-size: 1rem">{{ order_info.order_price.substring(order_info.order_price.length - 3,order_info.order_price.length) }}</span>
                         </span>
                        </template>

                   </div>
                   <!--拼团成功显示的层-->
                   <div v-if="order_info.goods_types == 'goods_group' && order_info.shipping_status == 0" class="group_success_ceil" @click="toGroupSuccess">
                        <span>拼单成功</span>
                        <div class="group_success_ceil_right">
                             <div v-for="(groupusImg,groupimgIns) in order_info.period.lists" class="user_img" :style="{backgroundImage:'url('+ groupusImg.headimgurl +')'}"></div>
                             <img src="@/common/image/publicImg/huiRight.png" alt="">
                        </div>
                   </div>
                   <div class="order_info border_radius_bottom border_radius_top">
                        <div style="display: flex;align-items: center">
                             <span>订单编号:{{ order_info.order_no }}</span>
                             <span class="copy" @click="copy(order_info.order_no)">复制</span>
                        </div>
                        <div style="margin-top: 1rem">
                             <span>下单时间:</span>
                             <span>{{ order_info.create_time }}</span>
                        </div>
                        <div style="margin-top: 1rem" v-if="order_info.pay_time != ''">
                             <span>付款时间:</span>
                             <span>{{ order_info.pay_time }}</span>
                        </div>
                        <div style="margin-top: 1rem" v-if="order_info.shipping_time != ''">
                             <span>发货时间:</span>
                             <span>{{ order_info.shipping_time }}</span>
                        </div>
                        <div style="margin-top: 1rem" v-if="order_info.success_time != ''">
                             <span>收货时间:</span>
                             <span>{{ order_info.success_time }}</span>
                        </div>

                   </div>
                   <div class="footer steep_padding_bottom">
                        <template v-if="order_info.goods_types != 'goods_group'">
                             <template v-if="order_info.goods_types.indexOf('goods_duobao') == -1">
                                  <span v-if="order_info.order_status == 0 || order_info.order_status == 1" @click.stop="show = true" class="clearSpan">取消订单</span>
                                  <span v-if="order_info.order_status == 0" class="spanRed pay_btn" @click="pay(order_info.order_no,order_info.order_price)">支付￥{{ order_info.pay_price }}</span>
                                  <span v-if="order_info.order_status == 1" @click="remind" class="waring_send">提醒发货</span>
                                  <!--<span @click="toLogisticsUrl" class="looked_address" v-if="order_info.order_status == 3">查看物流</span>-->
                                  <!--<span v-if="(order_info.order_status >= 1 && order_info.order_status != 2)" style="background-color: #e7204f;border: none; color: #ffffff;margin-left: 1rem" @click="againBuy(order_info.order_no)" >再来一单</span>-->
                             </template>
                             <span @click.stop="lengthen(order_info.order_no)" class="lengthen" v-if="order_info.order_status == 3">延长收货</span>
                             <span class="shore_get" v-if="order_info.order_status == 3" @click="showOrder">确认收货</span>
                             <span class="comment_btn" v-if="order_info.order_status == 4" @click="comment(order_info.order_no,false)">立即评价</span>
                             <span @click="deleteOrderDe" class="clearSpan" v-if="order_info.order_status == 2">删除订单</span>
                        </template>
                        <template v-if="order_info.goods_types == 'goods_group'">
                             <!--<span class="clearSpan" >删除订单</span>-->
                             <span @click.stop="show = true" class="clearSpan" v-if="order_info.order_status == 0 || (order_info.order_status == 1 && order_info.period.info.current == 1)">取消订单</span>
                             <span v-if="order_info.order_status == 1 && order_info.shipping_status == 0" @click="remind" class="waring_send">提醒发货</span>
                             <span @click="shareTeam" class="group_invent" v-if="order_info.order_status == 1 && order_info.shipping_status == -1">邀请好友拼单</span>
                             <span v-if="order_info.order_status == 0" class="spanRed pay_btn" @click="pay(order_info.order_no,order_info.order_price)">支付￥{{ order_info.pay_price }}</span>
                             <span @click.stop="lengthen(order_info.order_no)" class="lengthen" v-if="order_info.order_status == 3">延长收货</span>
                             <span class="shore_get" v-if="order_info.order_status == 3" @click="showOrder">确认收货</span>
                             <span class="comment_btn" v-if="order_info.order_status == 4" @click="comment(order_info.order_no,false)">立即评价</span>
                             <span @click="deleteOrderDe" class="clearSpan" v-if="order_info.order_status == 2">删除订单</span>
                        </template>
                   </div>
                   <!--支付过程显示的弹窗-->
                   <div class="show-shore" v-if="showAlert2">
                        <h3>确认支付是否成功</h3>
                        <div>
                             <span @click="shorePay()">确定</span>
                        </div>
                   </div>
                   <div class="show-logistics" v-if="showUrl">
                        <iframe id="iframeUrl" :src=order_info.logistics_url >
                        </iframe>
                        <div @click="showUrl = false" style="position: absolute;top: 0;width: 7rem;height:5rem;z-index: 900;">
                        </div>
                   </div>
              </div>
          </template>
     </div>
</template>

<script>
     import HeaderPage from '../components/HeaderPage'
     import storage from "@/common/js/storage.js"
     import { Dialog } from 'vant';
     export default {
          name: "OrderEetails",
          data(){
               return {
                    order_info:null,
                    showAlert2:false,
                    show:false,
                    radio: '-1',
                    showUrl:false,
                    //系统时间戳
                    xttimestamp:0,
                    //等待付款的时间
                    payOverTime:3600,
                    payOverTimeStr:'',
                    userInfo:null
               }
          },
          mounted(){
          },
          methods:{
               //到店铺
               toShop(){
                    this.$router.push({name:"shop",query:{shop_id:this.order_info.shop_id}})
               },
               //到发票详情
               toBillDetail(){
                    this.$store.dispatch("setOrderBillInfo",this.order_info)
                    this.$router.push({
                         name:"billdetail"
                    })
               },
               //调取商品详情
               getComInfo(goods_id, callback) {
                    var obj = {
                         goods_id
                    }
                    this.postObj(obj, window.PATHS.SHOPS_GOODS_GET_INFO, (result) => {
                         if (result.code == 1) {
                              callback(result.data)
                         } else {
                              callback(false)
                         }
                    })
               },
               //邀请好友拼单
               shareTeam() {
                    var obj = {
                         title: this.order_info.goods[0].title,
                         content: "拼团优惠",
                         url: window.HOST + "/goods/group?uid=" + this.userInfo.id + "&goods_id=" + this.order_info.goods[0].goods_id + "&period_id=" + this.order_info.goods[0].period_id,
                         imgsrc: this.order_info.goods[0].thumb,
                         nickname: this.userInfo.nickname,
                         headimgurl: this.userInfo.headimgurl,
                    }
                    this.getComInfo(this.order_info.goods[0].goods_id, (data) => {
                         if (data) {
                              obj.shop_price = data.shop_price,
                                  obj.market_price = data.market_price,
                                  obj.sales = data.sales,
                                  obj.needPersor = data.extra_data.number - data.extra_data.current,
                                  obj.duration = data.extra_data.duration,
                                  obj.extra_type = data.extra_table
                              console.log(obj)
                              this.shareSame(obj)
                         }
                    })
               },
               //删除订单
               deleteOrderDe(){
                    Dialog.confirm({
                         title: '删除订单',
                         message: '确认要删除该订单吗'
                    }).then(() => {
                         var obj = {
                              order_no:this.order_info.order_no
                         }
                         this.postObj(obj, window.PATHS.ORDER_UDELORDER, (result) => {
                              if (result.code == 1) {
                                   storage.set("orderPageRefaus",true)
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },500)
                              }
                         })
                    }).catch(() => {
                    });
               },
               //延长收货
               lengthen(order_no) {
                    Dialog.confirm({
                         message: '确认延长收货时间？每笔订单只能延长一次哦',
                         confirmButtonColor: "#f55056",
                         cancelButtonColor: "#f55056"
                    }).then(() => {
                         var obj = {
                              order_no
                         }
                         this.postObj(obj,window.PATHS.SHOPSORDER_CONFIRMDEFER,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("延长收货成功")
                              }
                         })
                    }).catch(() => {
                         // on cancel
                    });
               },
               //查看售后
               findaftersale(item){
                    this.$router.push({name:"afterlist",query:{order_no:this.order_info.order_no, order_goods_id:item.aid}})
               },
               //到申请售后
               aftersale(val){
                    val.time = this.xttimestamp
                    this.$store.dispatch("setAfterInfo",val)
                    this.$store.dispatch("setAfterOrderInfo",this.order_info)
                    location.href = "#/afterindex"
               },
               //到拼团成功
               toGroupSuccess(){
                    this.$store.dispatch("setGroupSuccess",this.order_info.period.lists)
                    console.log()
                    location.href = "#/ordersuccess"
               },
               //确认收货
               showOrder(){
                    Dialog.confirm({
                         title: '确认收货',
                         message: '确认已经收货货物，避免人财两空'
                    }).then(() => {
                         this.affirm(this.order_info.order_no)
                    }).catch(() => {
                         // on cancel
                    });
               },
               //提醒发货
               remind(){
                    Dialog.alert({
                         confirmButtonColor:"#f55056",
                         message: '卖家已收到你的提醒，会尽快发货'
                    }).then(() => {
                         // on close
                    });
               },
               //到商品详情
               toComDetail(good_id){
                    this.$router.push({name: 'DetailsPage', query: {comId: good_id}})
               },
               toLogisticsUrl(){
                    this.showUrl = true
                    // var urlStyle = document.getElementById('iframeUrl').contentWindow.document.getElementsByClassName("g-header")
                    // urlStyle.style.display = "none";
               },
               //再来一单
               // againBuy(order_no){
               //      this.$store.commit('setagainOrder',order_no)
               //      location.href = "#/againorder"
               // },
               //立即评价与追评
               comment(order_no,bool){
                    this.postObj({order_no},window.PATHS.SHOPS_ORDER_GET_INFO,(result) => {
                         if (result.code == 1){
                              if (result.data.goods.length > 1){
                                   this.$store.commit('setCommentOrder',order_no)
                                   location.href = '#/comment'
                              } else {
                                   if (bool){
                                        this.$router.push({name:'commenteeild',params:{comInfo:result.data.goods[0],order_no,bool}})
                                   } else {
                                        this.$router.push({name:'commenteeild',params:{comInfo:result.data.goods[0],order_no}})
                                   }
                              }
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //确认收货
               affirm(order_no){
                    var that = this
                         this.postObj({order_no},window.PATHS.ORDER_SHIOOINGORDER,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("收货成功")
                                   storage.set("orderPageRefaus",true)
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },1500)
                              }else{
                                   this.$toast.center(result.info)
                              }
                         })
               },
               //获取该订单信息
               getInfo(){
                    var order_no = this.$route.query.order_no
                    //获取vuex传递过来的order_no
                    this.postObj({order_no},window.PATHS.SHOPS_ORDER_GET_INFO,(result) => {
                         if (result.code == 1){
                              this.order_info = result.data
                              this.userInfo = result.user
                              console.log(this.order_info,"订单详情")
                              this.xttimestamp = result.timestamp
                              //如果是未付款显示倒计时
                              if (this.order_info.order_status == 0){
                                   window.payTimeOver = setInterval(() => {
                                        this.payOverTime--
                                        this.payOverTimeStr = this.changeTime(this.payOverTime)
                                   },1000)
                              }
                         }
                    })
               },
               //取消订单
               deleteOrder() {
                    var that = this
                         var obj = {}
                         obj.order_no = this.$route.query.order_no
                         if (this.radio != "-1"){
                              if (this.radio == "1") {
                                   obj.remark = "我不想买了"
                              }
                              if (this.radio == "2") {
                                   obj.remark = "信息填写错误重新拍"
                              }
                              if (this.radio == "3") {
                                   obj.remark = "卖家缺货"
                              }
                              if (this.radio == "4") {
                                   obj.remark = "同城见面交易"
                              }
                              if (this.radio == "5") {
                                   obj.remark = "其他原因"
                              }
                         }else{
                              this.$toast.center("请选择您取消订单的原因")
                              return
                         }

                         this.postObj(obj, window.PATHS.ORDER_CANCELORDER, (result) => {
                              console.log(result)
                              this.show = false
                              setTimeout(() => {
                                   that.clearOrderBool = true
                              },1500)
                              if (result.code == 1){
                                   this.$toast.center('取消成功')
                                   storage.set("orderPageRefaus",true)
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },1500)
                              }else{
                                   this.$toast.center(result.info)
                              }
                         })
               },
               //确认用户是否付钱了
               shorePay(){
                    var that = this
                    this.showAlert2 = false
                    this.postObj({order_no:this.order_info.order_no},window.PATHS.ORDER_GETORDER,(data) => {
                         if (data.code == 1){
                              if (data.data.pay_status == 1){
                                   this.$toast.center('支付成功')
                                   storage.set("orderPageRefaus",true)
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },1500)
                                   return
                              }else {
                                   this.$toast.center('支付失败')
                                   setTimeout(() => {
                                        window.history.go(-1)
                                   },1500)
                              }
                         }else {
                              this.$toast.center(data.info)
                         }
                    })
               },
               copy(order_no){
                   location.href = "app://send/copy/?" + order_no
               },
          //     支付成功的方法
          //     付款
               pay(order_no,price){
                    var url = "app://shoporder/pay/" + order_no + '/' + price + '?type=all'
                    location.href = url
               },
          //     等待付款倒计时
          },
          created(){
               this.getInfo()
          },
          computed:{
               //将时间戳转换成天时分
               time(){
                    var times = ""
                    var timeSec = this.order_info.success_expire - this.xttimestamp
                         // var min = Math.floor(timeSec%3600);
                         // var times = Math.floor(timeSec/3600) + ":" + (Math.floor(min/60) < 10? '0'+Math.floor(min/60):Math.floor(min/60)) + ":"+ (timeSec % 60 < 10? '0' + timeSec % 60 : timeSec % 60);
                         var day = Math.floor(timeSec%86400)
                         var horver = Math.floor(day % 3600)
                         var seday = Math.floor(timeSec/86400) + "天"
                         var hour = Math.floor(horver/3600) <= 0? "00时" : Math.floor(horver/3600) < 10? ('0' + Math.floor(horver/3600)+ '时') : Math.floor(horver/3600)  + "时"
                         times = seday + hour
                         return times;
               },
          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to,from,next){
               clearInterval(window.payTimeOver)
               if (to.name == "DetailsPage") {
                    to.meta.keepLoadBool = true
               }
               next()
          }
     }
</script>




