
<style scoped lang="scss">
     #after_detail{
          height: 100vh;
          background-color: #f6f6f6;
          overflow-y: scroll;
     }
.price_history{
     background-color: #ffffff;
     border-radius: 7px;
     margin-top: 7px;
     >div{
          padding: 14px 12px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          >span{
               font-size: 1.3rem;
          }
     }
     img{
          width: 0.6rem;
          height: 1.2rem;
     }
     .price_return{
          font-size: 1.1rem;
          color: #f55056;
          span{font-family: 数字专用}
     }
}
     .shop_detail_info{
          padding: 14px 12px;
          background-color: #ffffff;
          border-radius: 7px;
          margin-top: 7px;
     }
     .shop_name{
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 1.5rem;
          >div{
               display: flex;
               align-items: center;
          }
     }
     .goods_info{
          display: flex;
          align-items: flex-start;
          justify-content: flex-start;
          font-size: 1.3rem;
          margin-top: 15px;
          .goods_thumb{
               width: 8.5rem;
               height: 8.5rem;
               border-radius: 5px;
               background-size: cover;
               flex-shrink: 0;
          }
          .goods_title{
               margin-left: 10px;
               p{
                    text-overflow: -o-ellipsis-lastline;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    line-clamp: 2;
                    -webkit-box-orient: vertical;
               }
               span{
                    display: inline-block;
                    margin-top: 10px;
                    color: #9d9d9d;
               }
          }
     }
     .after_info{
          ul{
               li{
                    margin-top: 10px;
                    font-size: 1.3rem;
                    color: #9d9d9d;
                    .zmImg{
                         width: 9.4rem;
                         height: 9.4rem;
                         background-size: cover;
                         margin-right: 7px;
                    }
               }
          }
     }
     .banner{
          width: 100%;
          height: 8.25rem;
          background-image: url("~@/common/image/aftersale/di.png");
          background-size: 100% 100%;
          position: relative;
          >div{
               position: absolute;
               top: 50%;
               left: 50%;
               transform: translate(-50%,-50%);
               width: 100%;
               display: flex;
               align-items: center;
               flex-direction: column;
          }
          h4{
               text-align: center;
               font-size: 1.7rem;
               font-weight: 600;
               color: #ffffff;
          }
     }
     .just_price{
     }
     .aftr_part{
          img{
               margin-top: 10px;
               width: 100%;
          }
     }
     .footer_btn{
          text-align: right;
          margin-top: 10px;
          .add_address{
               display: inline-block;
               line-height: 2.9rem;
               width: 10.5rem;
               text-align: center;
               background-color: #f55056;
               border-radius: 1.45rem;
               font-size: 1.3rem;
               color: #ffffff;
          }
     }
     .zmImg_part{
          display: flex;
          justify-content: flex-start;
     }
</style>

<template>
     <!--退款单详情-->
     <div id="after_detail">
          <div class="header">
               <HeaderPage>
                    <span slot="headerTitle">退货退款/售后详情</span>
               </HeaderPage>
          </div>
          <div class="banner">
               <div>

                    <div v-if="adInfo" class="aftr_part">
                         <!--仅退款-->
                         <div class="just_price" v-if="adInfo.refund_type == '1'">
                              <h4 v-if="adInfo.status == '0'">买家申请，待商家确认</h4>
                              <h4 v-if="adInfo.status == '2'">售后失败</h4>
                              <h4 v-if="adInfo.status == '5'">已撤销</h4>
                              <h4 v-if="adInfo.status == '6'">售后成功</h4>
                              <img v-if="adInfo.status == '0'" src="@/common/image/aftersale/justprice/status0.png" alt="">
                              <img v-if="adInfo.status == '2'" src="@/common/image/aftersale/justprice/status2.png" alt="">
                              <img v-if="adInfo.status == '6'" src="@/common/image/aftersale/justprice/status6.png" alt="">
                         </div>
                         <!--退货退款-->
                         <div class="reprice_regoods" v-if="adInfo.refund_type == '2'">
                              <h4 v-if="adInfo.status == '0'">买家申请，待商家确认</h4>
                              <h4 v-if="adInfo.status == '1'">商家已确认，待买家发货</h4>
                              <h4 v-if="adInfo.status == '2'">售后失败</h4>
                              <h4 v-if="adInfo.status == '3'">买家已发货</h4>
                              <h4 v-if="adInfo.status == '5'">已撤销</h4>
                              <h4 v-if="adInfo.status == '6'">售后成功</h4>
                              <img v-if="adInfo.status == '0'" src="@/common/image/aftersale/retugodsprice/status0.png" alt="">
                              <img v-if="adInfo.status == '1'" src="@/common/image/aftersale/retugodsprice/status1.png" alt="">
                              <img v-if="adInfo.status == '2'" src="@/common/image/aftersale/retugodsprice/status2.png" alt="">
                              <img v-if="adInfo.status == '3'" src="@/common/image/aftersale/retugodsprice/status3.png" alt="">
                              <img v-if="adInfo.status == '6'" src="@/common/image/aftersale/retugodsprice/status6.png" alt="">
                         </div>
                         <!--退换货-->
                         <div class="retun_goods" v-if="adInfo.refund_type == '3'">
                              <h4 v-if="adInfo.status == '0'">买家申请，待商家确认</h4>
                              <h4 v-if="adInfo.status == '1'">商家已确认，待买家发货</h4>
                              <h4 v-if="adInfo.status == '2'">售后失败</h4>
                              <h4 v-if="adInfo.status == '3'">买家已发货，待商家确认</h4>
                              <h4 v-if="adInfo.status == '5'">已撤销</h4>
                              <h4 v-if="adInfo.status == '6'">售后成功</h4>
                              <img v-if="adInfo.status == '0'" src="@/common/image/aftersale/changegoods/status0.png" alt="">
                              <img v-if="adInfo.status == '1'" src="@/common/image/aftersale/changegoods/status1.png" alt="">
                              <img v-if="adInfo.status == '2'" src="@/common/image/aftersale/changegoods/status2.png" alt="">
                              <img v-if="adInfo.status == '3'" src="@/common/image/aftersale/changegoods/status3.png" alt="">
                              <img v-if="adInfo.status == '6'" src="@/common/image/aftersale/changegoods/status6.png" alt="">
                         </div>
                    </div>
               </div>
          </div>
          <div v-if="adInfo" class="price_history">
               <div>
                    <span>退款总金额</span>
                    <div class="price_return">
                         <span>￥</span><span style="font-size: 1.5rem">{{ adInfo.refund_price.substring(0,adInfo.refund_price.length - 3) }}</span><span>{{  adInfo.refund_price.substring(adInfo.refund_price.length - 3,adInfo.refund_price.length) }}</span>
                    </div>
               </div>
               <div @click="toHistory">
                    <span>协商历史</span>
                    <img src="@/common/image/publicImg/huiRight.png" alt="">
               </div>
          </div>
          <div v-if="adInfo" class="shop_detail_info">
               <div class="shop_name">
                    <div class="shop_anme_left">
                         <span>{{ adInfo.shop_name }}</span>
                         <img src="@/common/image/publicImg/huiRight.png" alt="" style="width: 0.55rem;height: 1rem;margin-left: 9px">
                    </div>
                    <div class="mess_shop">
                         <img src="@/common/image/aftersale/lianxishangjia.png" alt="" style="width: 1.5rem;height: 1.5rem;margin-right: 9px;">
                         <span>联系商家</span>
                    </div>
               </div>
               <div class="goods_info">
                    <div class="goods_thumb" :style="{backgroundImage:'url('+ adInfo.thumb +')'}"></div>
                    <div class="goods_title">
                         <p>{{ adInfo.title }}</p>
                         <span>{{ adInfo.specs }} </span>
                    </div>
               </div>
               <div class="after_info">
                    <ul>
                         <li>
                              <span v-if="adInfo.refund_type == '1'">售后类型: 仅退款</span>
                              <span v-if="adInfo.refund_type == '2'">售后类型: 退款退货</span>
                              <span v-if="adInfo.refund_type == '3'">售后类型: 仅换货</span>
                         </li>
                         <li>退货原因: {{ adInfo.reason }}</li>
                         <li>退货金额: {{ adInfo.refund_price }}</li>
                         <li>申请时间: {{ adInfo.create_time }}</li>
                         <li>售后单号: {{ adInfo.refund_order_no }}</li>
                         <li class="zmImg_part" v-if="adInfo.picture.length != 0">
                              <div class="zmImg" v-for="(img,imgindex) in adInfo.picture" :key="imgindex + 'img'" :style="{backgroundImage: 'url('+ img +')'}">
                              </div>
                         </li>
                    </ul>
               </div>
               <div class="footer_btn" v-if="(adInfo.refund_type == '2' || adInfo.refund_type == '3') && adInfo.status == '1'" @click="addAddress">
                    <span  class="add_address">填写物流信息</span>
               </div>

          </div>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "AfterDetail",
          data(){
               return{
                    adInfo:null,
               }
          },
          methods:{
          //     到协商历史
               toHistory(){
                    this.$router.push({name:"afterhistory",query:{refund_id:this.adInfo.aid}})
               },
          //     到添加地址
               addAddress(){
                    this.$store.dispatch("setAfterDetail",this.adInfo)
                    location.href  = "#/addaddress"
               },
          //     获取售后详情
               getAfterDetailInfo(){
                    var obj = {
                         refund_id:this.$route.query.refund_id
                    }
                    this.postObj(obj,window.PATHS.aftersale.SHOPSREFUND_GETORDERINFO,(result) => {
                         if (result.code == 1){
                              this.adInfo = result.data
                              console.log(this.adInfo)
                         }
                    })
               }
          },
          created() {
               this.getAfterDetailInfo()
          },
          components:{
               HeaderPage
          }
     }
</script>





