<style scoped lang="scss">
#bill_rules{
     height: 100vh;
     background-color: #f6f6f6;
     overflow-y: scroll;
}
     .bill_rules_header{
          position: fixed;
          width: 100%;
          background-color: #ffffff;
          top: 0;
     }
     .bill_rules_content{
          padding: 0 1.2rem;
          padding-top: 5rem;
          p{
               font-size: 1.3rem;
               color: #9d9d9d;
               line-height: 2.5rem;
          }
     }
</style>

<template>
     <!--发票规则-->
     <div id="bill_rules" class="steep_padding">
          <div class="bill_rules_header">
               <HeaderPage>
                    <span slot="headerTitle">发票规则</span>
               </HeaderPage>
          </div>
          <div class="bill_rules_content">
               <p>一、发票金额仅开具实付金额（不包含优惠券、红包、促销满减等）。
                    二、麦达汇官方销售的商品由商品销售主体公司开具发票，第三方商家销售的商品/服务发票由第三方商家开具，发票类型（电子发票、纸质发票、增值税专用发票）由卖家根据实际商品/服务开具。（增值税专用发票仅限公司主体开具）
                    三、请确保发票抬头、纳税人识别号或统一社会信用代码的准确性；自2017年7月1日起，增值税普通发票没填写纳税人识别号或统一社会信用代码不得作为税收凭证。
                    四、发票类型
                    1．普通发票
                    普通发票是指在购销商品、提供或接受服务以及从事其他经营活动中，所开具和收取的收付款凭证。它是相对于增值税专用发票而言的。
                    任何单位和个人在购销商品、提供或接受服务以及从事其他经营活动中，除增值税一般纳税人开具和收取的增值税专用发票之外，所开具和收取的各种收付款凭证均为普通发票。
                    2．电子普通发票
                    电子发票与纸质发票具备同等法律效力，可作为用户维权、保修的有效凭据；其法律效力、基本用途及使用规定同纸质发票，如需纸质发票可自行下载打印。
                    3．增值税专用发票
                    增值税专用发票是由国家税务总局监制设计印制的，只限于增值税一般纳税人领购使用的，既作为纳税人反映经济活动中的重要会计凭证又是兼记销货方纳税义务和购货方进项税额的合法证明；是增值税计算和管理中重要的决定性的合法的专用发票。</p>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "BillRules",
          components:{
               HeaderPage
          }
     }
</script>





