                              <span v-if="customization != null" style="margin-right: 11px;">共1件</span>
                              <span style="font-size: 1rem;color: #9d9d9d">￥</span>
                              <span v-if="skuCom != null" class="num_family" style="font-size: 1.4rem;font-weight: 500">{{ allPrice }}</span>
                              <span v-if="customization != null" class="num_family"
                                    style="font-size: 1.4rem;font-weight: 500">{{ customization.goods_price }}</span>
                         </div>
                    </div>
                    <div class="become_getprce">
                         <span>快递无忧：</span>
                         <div>
                              <span style="font-size: 1rem;color: #9d9d9d">+￥</span>
                              <span class="num_family" style="font-size: 1.4rem;font-weight: 500">0</span>
                         </div>
                    </div>

               </div>
               <!-- 快捷支付 -->
               <div class="fast_payment">
                    <h5>快捷支付:</h5>
                    <div class="pay_type">
                         <!-- 余额足够时 -->
                         <template v-if="comInfo.extra_table != 'goods_customs'">
                              <section v-if="userInfo.votes / 1 >= allPrice / 1" @click="comShore('votes')">
                                   <div>
                                        <img class="balance_icon" src="@/common/image/entcom/yuehave.png" alt="">
                                        <span>
                                             <span style="margin-right:10px;">余额</span>
                                             <span style="color:#e00000">
                                                  (<span style="font-size:1.2rem;">￥</span><span>{{ userInfo.votes.substr(0,userInfo.votes.length - 3) }}</span><span
                                                     style="font-size:1.2rem">{{ userInfo.votes.substr(userInfo.votes.length - 3,userInfo.votes.length) }}</span>)
                                             </span>
                                        </span>
                                   </div>
                                   <img class="pat_right" src="@/common/image/entcom/payright.png" alt="">
                              </section>
                              <!-- 余额不够时 -->
                              <section v-if="userInfo.votes / 1 < allPrice / 1">
                                   <div>
                                        <img class="balance_icon" src="@/common/image/entcom/noneprice.png" alt="">
                                        <span class="noe_votes">
                                             <span style="margin-right:10px;">余额不足</span>
                                             <span>
                                                  (<span style="font-size:1.2rem;">￥</span><span>{{ userInfo.votes.substr(0,userInfo.votes.length - 3) }}</span><span
                                                     style="font-size:1.2rem">{{ userInfo.votes.substr(userInfo.votes.length - 3,userInfo.votes.length) }}</span>)
                                             </span>
                                        </span>
                                   </div>
                              </section>
                         </template>
                         <section @click="comShore('alipay')">
                              <div>
                                   <img class="alipay_icon" src="@/common/image/entcom/zhifubao.png" alt="">
                                   <span>支付宝</span>
                              </div>
                              <img class="pat_right" src="@/common/image/entcom/payright.png" alt="">
                         </section>
                         <section @click="comShore('wechat')" v-if="comInfo.extra_table != 'goods_customs'">
                              <div>
                                   <img class="alipay_icon" src="@/common/image/entcom/wechaticon.png" alt="">
                                   <span>微信支付</span>
                              </div>
                              <img class="pat_right" src="@/common/image/entcom/payright.png" alt="">
                         </section>
                         <!-- 花呗 -->
                         <section class="last_section" @click="checkedQi = !checkedQi" v-if="comInfo.extra_table != 'goods_customs'">
                              <div>
                                   <img class="alipay_icon" src="@/common/image/entcom/checkicon.png" alt="">
                                   <span>花呗支付</span>
                              </div>
                              <img :class="checkedQi? 'alipay_num' : ''" class="pat_right"
                                   src="@/common/image/entcom/payright.png" alt="">
                         </section>
                         <ul v-if="checkedQi">
                              <li @click="comShore('alipay',3)">
                                   <div>
                                        <span class="how_qi">￥{{ thrExpect }}*3期</span>
                                        <span class="qi_price">(手续费￥2.3/期)</span>
                                   </div>
                                   <img src="@/common/image/entcom/payright.png" alt="">
                              </li>
                              <li @click="comShore('alipay',6)">
                                   <div>
                                        <span class="how_qi">￥{{ sixExpect }}*6期</span>
                                        <span class="qi_price">(手续费￥4.5/期)</span>
                                   </div>
                                   <img src="@/common/image/entcom/payright.png" alt="">
                              </li>
                              <li @click="comShore('alipay',12)">
                                   <div>
                                        <span class="how_qi">￥{{ twelveExpect }}*12期</span>
                                        <span class="qi_price">(手续费￥7.5/期)</span>
                                   </div>
                                   <img src="@/common/image/entcom/payright.png" alt="">
                              </li>
                         </ul>
                    </div>
               </div>
               <!--海外购说明-->
               <div class="customs_explain" v-if="comInfo.extra_table == 'goods_customs'">
                    <van-checkbox v-model="checked" icon-size="14" checked-color="#f55056">
                         <span class="text">购买跨境商品需同意<a @click="tocustor" style="color: #f55056">《用户购买须知》</a></span>
                    </van-checkbox>
               </div>
               <!--可用40麦穗抵扣-->
               <div class="Points-deduction same" v-if="false">
                    <div>
                         <span>可用</span>
                         <span style="color: #e7204f">40</span>
                         <span>麦穗抵扣</span>
                         <span style="color: #e7204f">4.00</span>
                         <span>元</span>
                    </div>
                    <div>
                         <van-checkbox v-model="checked" checked-color="#e7204f">

                         </van-checkbox>
                    </div>
               </div>
               <div class="entcom-footer">

                    <div v-if="memberPremium" class="footer_etprice">
                         <span>本单赚￥<span class="num_family">{{ comInfo.rebate_price }}</span>元</span>
                    </div>
                    <div class="sent_order">
                         <div>
                              <div class="goods_price_footer" style="display: flex;align-items: center">
                                   <span class="practical_price" style="font-size: 1.5rem">实付:</span>
                                   <span v-if="skuCom != null" class="price">￥<span style="font-size: 1.5rem">{{ allPrice.substring(0,allPrice.length - 3) }}</span>{{ allPrice.substring(allPrice.length - 3,allPrice.length) }}
                                        </span>
                                   <!--定制版-->
                                   <span v-if="customization != null" style="font-size: 1.7rem;color: #e7204f">￥{{ customization.goods_price }}</span>
                              </div>
                         </div>
                         <button class="shore" @click="comShore('all')">
                              提交订单
                         </button>
                         <!--<span class="shore" @click="comShore('all')">提交订单</span>-->
                         <!--<span class="shore" @click="">继续支付</span>-->
                    </div>
               </div>
               <div class="hiddenBlack" v-if="showAlert2"></div>
               <!--底部弹出省市区-->
               <van-popup v-model="cityShow" position="bottom" :style="{ height: '40%' }">
                    <van-area :value="cityArr.length == 0? '' : cityArr[2].code" @cancel="clearCtity"
                              @confirm="shorePlace" :area-list="areaList"/>
               </van-popup>
          </div>
     </div>
</template>

<script>
     import storage from '@/common/js/storage.js'
     import area from '@/common/js/area.js'
     import InstallmentPage from '../components/InstallmentPage'
     import HeaderPage from "../components/HeaderPage"
     import {Dialog} from 'vant';

     export default {
          name: "EntcomPage",
          data() {
               return {
                    // 花呗分期的开关
                    checkedQi: false,
                    areaList: area,
                    cityShow: false,
                    //会员特效开关
                    memberPremium: false,
                    //看不到价格的开关
                    nonePrice: false,
                    showAlert2: false,
                    checked: false,
                    comInfo: null,
                    //备注
                    remark: "",
                    //sku信息
                    addressList: [],
                    address: "",
                    disableBtn: true,
                    timeNum: 0,
                    orderNo: '',
                    skuCom: null,
                    //门店信息
                    storeInfo: {},
                    billInfo: {},
                    //定制的信息
                    customization: null,
                    remarkBoole: false,
                    //收货人手机
                    userPhone: null,
                    //收货人详细地址
                    addressDetail: null,
                    //省市区
                    addressCity: "",
                    //收货人姓名
                    addrensName: null,
                    errormessage: '',
                    cityArr: [],
                    allPrice: null,
                    warningRedBool: true,
                    userInfo: null,
                    scrollTop:90,
                    userInfo:null,
                    rule:null
               }
          },
          methods: {
               // 丹丹省活动
               _everyS(){
                    if(this.userInfo.is_agent == 1 || this.userInfo.vip_status == 1){
                         return
                    }
                    var obj = {
                         name:"1.88临时会员福利"
                    }
                    this.postObj(obj,window.PATHS.MEMBERVIP_RULE,(result) => {
                         this.rule = result.data
                    })
               },
               //查看海外规则
               tocustor(){
                    location.href = "app://view/"+ window.HOST +"/pages/customs"
               },
               //点击地址
               showCity() {
                    this.cityShow = true
               },
               //地址取消
               clearCtity() {
                    this.cityShow = false
               },
               //地址确定
               shorePlace(arr) {
                    this.addressCity = ""
                    arr.forEach((item, index) => {
                         this.addressCity += item.name
                         if (index != arr.length - 1) {
                              this.addressCity += '/'
                         }
                    })
                    this.cityArr = arr
                    this.cityShow = false
               },
               //聚焦手机号
               focuPhone() {
                    this.errormessage = ''
               },
               //手机号识别
               malePhone() {
                    if (this.userPhone.length == 0) {
                         this.errormessage = ''
                    } else {
                         if (!(/^1[3456789]\d{9}$/.test(this.userPhone))) {
                              this.errormessage = "手机号有误"
                         }
                    }
               },
               //返回上一页
               toBack() {
                    if (this.$route.query.commodity_json) {
                         location.href = window.EQUITMENTBACK
                    } else {
                         window.history.go(-1)
                    }
               },
               //确认用户支付是否成功
               shorePay() {
                    this.showAlert2 = false
                    this.getAddress()
                    this.postObj({order_no: this.orderNo}, window.PATHS.ORDER_GETORDER, (data) => {
                         if (data.code == 1) {
                              if (data.data.pay_status == 1) {
                                   if (data.data.order_status == 2) {
                                        if (this.comInfo.extra_table == 'goods_group') {
                                             this.$toast.center("拼团失败")
                                        }
                                   } else {
                                        this.$store.commit('setOrderNo', this.orderNo)
                                        location.replace("#/paysuccess")
                                        return
                                   }
                              } else {
                                   this.$store.commit('setOrderNo', this.orderNo)
                                   location.replace("#/paydefeated")
                              }
                         } else {
                              this.$toast.center(data.info)
                         }
                    })
               },
               toPlace() {
                    location.href = "#/place"
               },
               //保存地址
               saveplace() {
                    if (this.addressList.length == 0) {
                         var setData = {};
                         setData.user_name = this.addrensName
                         setData.user_phone = this.userPhone
                         setData.user_address = this.addressDetail
                         setData.isdefault = 1;
                         setData.provinceName = this.cityArr[0].name;
                         setData.cityName = this.cityArr[1].name;
                         setData.countryName = this.cityArr[2].name;
                         setData.detailInfo = " ";
                         setData.nationalCode = '86';
                         this.postObj(setData, window.PATHS.ADDRESS_SAVE_INFO, (result) => {
                         })
                    }
               },
               // 活动下订单
               activeAddOrder(ruleId,callBack){
                    var obj = {
                         rule_id:ruleId
                    }
                    this.postObj(obj,window.PATHS.MEMBERVIP_ADDORDER,(result) => {
                         if(result.code == 1){
                              callBack(result.data)
                         }
                    })
               },
               // 合并订单
               orderGentHe(arr,callBack){
                    var obj = {
                          order_nos:arr
                    }
                    this.postObj(obj,window.PATHS.ORDER_MERGEORDER,(result) => {
                         if(result.code == 1){
                              callBack(result.data)
                         }
                    })
               },
               //确认支付
               comShore(type, hb_fq_num) {
                    //海外购是否实名认证
                    if (this.comInfo.extra_table == 'goods_customs' && this.userInfo.user_auth == 0) {
                         Dialog.confirm({
                              message: '购买当前商品需实名认证'
                         }).then(() => {
                              location.href = "app://auth"
                         }).catch(() => {
                         });
                         return
                    }
                    var shopOrder = {
                         goods: []
                    }
                    //定制
                    if (this.customization != null) {
                         shopOrder.goods.push({
                              goods_id: this.customization.goods_id,
                              sku_path: this.customization.sku_path,
                              number: 1,
                         })
                    } else {
                         shopOrder.goods.push({
                              goods_id: this.skuCom.goods_id,
                              sku_path: this.skuCom.sku_path,
                              number: this.skuCom.number,
                              is_original: this.skuCom.is_original,
                              period_id: this.skuCom.period_id,
                              share_user_id: this.$store.state.shareId || 0,
                              video_id: this.skuCom.video_id
                         })
                    }
                    //地址
                    if (this.address) {
                         shopOrder.receiver_name = this.address.user_name
                         shopOrder.receiver_phone = this.address.user_phone
                         shopOrder.receiver_address = this.address.provinceName + " " + this.address.cityName + " " + this.address.countryName + " " + this.address.user_address
                    } else {
                         if (this.addrensName && this.userPhone && this.cityArr.length != 0 && this.addressDetail) {
                              shopOrder.receiver_name = this.addrensName
                              shopOrder.receiver_phone = this.userPhone
                              shopOrder.receiver_address = this.cityArr[0].name + " " + this.cityArr[1].name + " " + this.cityArr[2].name + " " + this.addressDetail
                         } else {
                              this.$toast.center("请完善收货信息")
                              return
                         }
                    }
                    //海外商品如果认证姓名与收货人姓名不一致，提示重新填写收货人姓名
                    if (this.comInfo.extra_table == 'goods_customs' && shopOrder.receiver_name != this.userInfo.true_name) {
                         Dialog.alert({
                              message: '该商品收货人姓名必须与认证姓名保持一致'
                         }).then(() => {
                         });
                         return
                    }
                    //备注
                    shopOrder.remark = this.remark
                    //店铺id
                    shopOrder.shop_id = this.comInfo.shop_id
                    if (this.customization != null) {
                         shopOrder.is_customized = 1
                         shopOrder.remark = this.customization.remark
                    }
                    if (this.$store.state.bill != null) {
                         shopOrder.invoice = this.billInfo
                    }
                    if (this.disableBtn) {
                         this.disableBtn = false
                         //保存地址
                         // this.saveplace()
                         //提交订单信息
                         this.postObj(shopOrder, window.PATHS.SHOPS_OREDER_ADD_ORDER, (data) => {
                              if (data.code == 1) {
                                   this.orderNo = data.data.order_no
                                   if (data.data.status == 1) {
                                        this.$toast("status==1")
                                   } else {
                                        var url = ""
                                        // 如果是海外只能使用支付宝
                                        if (this.comInfo.extra_table == 'goods_customs') {
                                             type = 'alipay'
                                        }
                                        // 如果选中了活动
                                        if(this.memberPremium){
                                             this.activeAddOrder(this.rule.aid,(val) => {
                                                  var arr = [val.order_no,data.data.order_no]
                                                  this.orderGentHe(arr,(val2) => {
                                                         url = "app://shoporder/pay/" + val2.order_no + '/' + val2.pay_price + '?type=' + type
                                                          if (hb_fq_num) {
                                                                 url += '&hb_fq_num=' + hb_fq_num
                                                            }
                                                             location.href = url
                                                  })
                                             })
                                        }else{
                                              url = "app://shoporder/pay/" + data.data.order_no + '/' + data.data.price + '?type=' + type
                                              if (hb_fq_num) {
                                                                 url += '&hb_fq_num=' + hb_fq_num
                                                            }
                                                             location.href = url

                                        }




                                   }
                              }
                         })
                    }

               },
               tobillpage() {
                    location.href = '#/bill'
               },
               toAddPlace() {
                    location.href = '#/newplace'
               },
               add() {
                    if (this.customization) {
                         return
                    } else {
                         if (this.comInfo.extra_table == "goods_group") {
                              return
                         }
                         if (this.skuCom.number >= this.skuCom.stock) {
                              return
                         } else {
                              this.skuCom.number++
                              this.allPrice = (this.skuCom.goods_price * this.skuCom.number * 100 / 100).toFixed(2)
                              this.allPrice+=''
                              if (this.comInfo.extra_table == 'goods_customs') {
                                   if (this.allPrice > 5000) {
                                        this.skuCom.number--
                                   }
                              }
                         }
                    }

               },
               jian() {
                    if (this.customization != null) {
                         return
                    } else {
                         if (this.skuCom.number <= 1) {
                              return
                         } else {
                              this.skuCom.number--
                              this.allPrice = (this.skuCom.goods_price * this.skuCom.number * 100 / 100).toFixed(2)
                              this.allPrice += ""
                         }
                    }
               },
               //获取地址
               getAddress() {
                    //     获取收货地址
                    this.postObj({}, window.PATHS.ADDRESS_GET_LIST, (data) => {
                         if (data.code == 1) {
                              this.addressList = data.data
                              if (this.$store.state.address != null && this.addressList.length != 0) {
                                   this.address = this.$store.state.address;
                              } else {
                                   this.address = this.addressList[0] || "";
                                   if (this.address == "") {
                                        Dialog.alert({
                                             message: '您还没有收货地址，请点击添加',
                                             confirmButtonText: '添加',
                                             confirmButtonColor: '#f55056'
                                        }).then(() => {
                                             this.$refs.inpName.focus()
                                        });
                                   }
                              }
                         }
                    })
               },
               firstGet() {
                    var that = this
                    if (this.$route.query.commodity_json) {
                         this.customization = this.decodeBase(this.$route.query.commodity_json)
                         this.remarkBoole = true
                         this.remark = this.customization.remark
                    } else {
                         // 传递过来的sku信息
                         this.skuCom = this.$store.state.entDetailSkuInfo
                         this.allPrice = (this.skuCom.goods_price * this.skuCom.number * 100 / 100).toFixed(2)
                    }
                    //通过商品id获取信息
                    this.postObj({goods_id: this.customization != null ? this.customization.goods_id : this.skuCom.goods_id}, window.PATHS.SHOPS_GOODS_GET_INFO, (data) => {
                         if (data.code == 1) {
                              this.comInfo = data.data
                              this.getAddress()
                         } else {
                              this.$toast.center("商品已下架")
                              Dialog.close()
                              setTimeout(() => {
                                   window.history.go(-2)
                              },1000)
                         }
                    })
               },
               scroll(){
                    this.scrollTop = this.$refs.entCom.scrollTop
               },
               updateData(){
                     this.remark = ""
                    this.memberPremium = false
                    this.rule = null
               }
          },
          mounted() {
               // 原生调用js方法查询订单是否支付成功
               window.orderDone = () => {
                    this.shorePay()
               }

          },
          created() {
               // this.getAddress()
               this.firstGet()
               // 调取用户详情
               this.getUserInfo((data) => {
                    this.userInfo = data
                    this._everyS()
               })
          },
          computed: {
               // 分三期
               thrExpect() {
                    return this.stagePrice(this.allPrice, 3, 2.3)
               },
               // 分六期
               sixExpect() {
                    return this.stagePrice(this.allPrice, 6, 4.5)
               },
               // 分十二期
               twelveExpect() {
                    return this.stagePrice(this.allPrice, 12, 7.5)
               }
          },
          activated() {
               this.getUserInfo((data) => {
                    this.userInfo = data
                    this._everyS()
               })
               if (this.$route.meta.keepLoadBool) {
                    this.firstGet()
                    this.disableBtn = true
                    this.checkedQi = false
               }else{
                    if ( this.$refs.entCom) {
                         this.$refs.entCom.scrollTop = this.scrollTop
                    }
               }
          },
          beforeRouteLeave(to, from, next) {
               if (to.name == 'place') {
                    from.meta.keepLoadBool = false
               } else {
                    this.updateData()
                    from.meta.keepLoadBool = true
               }
               next()
          },
          beforeRouteEnter(to,from,next){
               if (from.name == 'place'){
                    next((vm) => {
                         vm.getAddress()
                    })
               }else{
                    next()
               }
          },
          watch:{
               memberPremium(newVal,oleVal){
                    if(newVal){
                         storage.set("entAllPrice",this.allPrice)
                         this.allPrice = (this.allPrice*100 + this.rule.price*100)/100
                         this.allPrice+=''
                    }else{
                        this.allPrice = storage.get("entAllPrice")
                    }
               }
          },
          components: {
               InstallmentPage,
               HeaderPage
          },
     }
</script>





