<style lang="scss" scoped>
.comment_report_header{
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #ffffff;
    box-shadow: 0 0 5px 5px #f6f6f6;
}
.comment_report_content{
    padding: 0 1.2rem;
    padding-top: 4.4rem
}
.choose_part{
    display: flex;
    justify-content: flex-start;
    padding-top: 3.45rem;
    ul{
        width:50%;
        li{
            margin-bottom: 2.5rem;
            font-size: 1.4rem
        }
    }
}
.text_area{
    textarea{
        width: 100%;
        height: 15rem;
        border: none;
        background-color: #f6f6f6;
        border-radius: 0.7rem;
        padding: 1.1rem 1.2rem;
        font-size: 1.2rem;
        color: #9d9d9d
    }
}
.footer_btn{
    text-align: right;
    margin-top: 1rem;
    span{
        width: 7rem;
        height: 2.9rem;
        display: inline-flex;
        align-items:center;
        justify-content:center;
        border-radius: 1.45rem;
        background-color: #f55056;
        font-size: 1.7rem;
        color: #ffffff;
    }
}
</style>

<template>
    <div id="comment_report" class="steep_padding">
            <div class="comment_report_header">
                    <HeaderPage>举报</HeaderPage>
            </div>
            <div class="comment_report_content">
                <div class="reaius">
                    <van-radio-group v-model="radio" icon-size="1.4rem" checked-color="#f55056">
                        <div class="choose_part">
                            <ul>
                                <li>
                                    <van-radio name="晒单图片与商品不符">晒单图片与商品不符</van-radio>
                                </li>
                                 <li>
                                    <van-radio name="黄赌毒暴力政治">黄赌毒暴力政治</van-radio>
                                </li>
                                 <li>
                                    <van-radio name="其他">其他</van-radio>
                                </li>
                            </ul>
                            <ul>
                                <li>
                                    <van-radio name="非法欺诈">非法欺诈</van-radio>
                                </li>
                                 <li>
                                    <van-radio name="广告灌水">广告灌水</van-radio>
                                </li>
                            </ul>
                        </div>
                    </van-radio-group>
                </div>
                <div class="text_area">
                    <textarea v-model="textArea" placeholder="可以补充您的投诉内容(选填)"></textarea>
                </div>
                <div class="footer_btn">
                    <span @click="submit">提交</span>
                </div>
            </div>
    </div>
</template>

<script>
import HeaderPage from "../../components/HeaderPage"
    export default {
        name:"CommentReport",
        data(){
            return{
                radio:"晒单图片与商品不符",
                textArea:""
            }
        },
        methods:{
            submit(){
                var obj = {
                    type:this.$route.query.type,
                    article_id:this.$route.query.article_id,
                    reason:this.radio
                }
                if(this.radio == "其他"){
                    if(this.textArea == ''){
                        this.$toast.center("请输入您要投诉的内容")
                        return
                    }else{
                        obj.content = this.textArea
                    }
                }
               console.log(obj)
                this.postObj(obj,window.PATHS.MEMBER_ADDCOMPLAINT,(result) => {
                    if(result.code == 1){
                        this.$toast.center("您的举报申请已提交，等待平台审核,页面即将跳转...")
                       setTimeout(() => {
                            window.history.go(-1)
                       },2000)
                    }
                })
            },
        },
        components:{
            HeaderPage
        }
    }
</script>>


