<style scoped lang="scss">


     .order-content > > > .van-popup--center {
          width: 65%;
     }

     .clear-order {
          position: absolute;
          z-index: 11;
          background-color: #ffffff;
          width: 100%;
          bottom: 0;
          padding-top: 1.5rem;
     }

     .redSpan {
          background-color: #f55056;
          color: #ffffff;
     }

     .order-content .li-footer span {
          display: inline-block;
          line-height: 2.9rem;
          width: 8.5rem;
          border-radius: 1.45rem;
          font-size: 1.5rem;
          text-align: center;
          margin-left: 1rem;
     }
     .order-content .li-footer {
          display: flex;
          margin-top: 1.5rem;
          justify-content: space-between;

          .bill_alt {
               display: flex;
               align-items: center;
               width: 3rem;
               img {
                    width: 1.4rem;
                    height: 0.3rem;
               }
          }
     }
     .com-content p {
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          /*line-height: 2rem;*/
     }

     .com-content {
          margin-top: 1rem;
          height: 8.5rem;
          display: flex;
          align-items: center;
          justify-content: flex-start;
     }

     .allOrder ul li h4 {
          margin-top: 10px;
          font-size: 1.3rem;
          color: #888888;
     }

     .allOrder ul li {
          padding: 1.05rem 1.45rem 1rem 1.5rem;
          background-color: #ffffff;
          margin-bottom: 1rem;
     }

     .allOrder ul li {
          border-radius: 7px;
     }

     .allOrder {
          padding: 0 12px;
          margin: 0 auto;
          margin-top: 10px;
          min-height: 100vh;
     }
     .allOrder ul{
          margin-bottom: 10rem;
     }

     .table ul {
          padding: 0 11px;
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 4rem;
          background-color: #ffffff;
          font-size: 1.5rem;

          li {
               height: 100%;
               display: flex;
               justify-content: center;
               align-items: center;
               color: #9d9d9d;

          }
     }

     .order-content {
          background-color: #f6f6f6;
          color: #212121;
          height: 100vh;
          position: relative;
          overflow: scroll;
     }

     .order_header {
          width: 100%;
          display: flex;
          align-items: center;
          background-color: #ffffff;
          // position: fixed;
          top: 0;
          z-index: 11
     }

     .top_flex {
          position: fixed;
          top: 0;
          z-index: 11;
          width: 100%;
     }

     .order_title {
          display: flex;
          justify-content: space-between;
          align-items: center;
          font-size: 1.5rem;

          .shop_info {
               img {
                    margin-left: 12px;
               }
          }

          > span {
               color: #ff0000;
          }
     }

     .order_goods_img {
          background-size: cover;
          width: 8.5rem;
          height: 100%;
          border-radius: 7px;
          flex-shrink: 0;
     }

     .order_goods_info {
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: space-between;
          margin-left: 10px;
          font-size: 1.3rem;
          .skuand_title {
               span {
                    margin-top: 6px;
                    color: #9d9d9d;
                    display: block;
               }
          }
          .order_goods_price {
               margin-left: 20px;

               .goods_info_right_price {
                    span {
                         font-family: 数字专用;
                    }
               }

               .buyHeader_price_num {
                    display: block;
                    text-align: right;
                    font-size: 1.2rem;
                    color: rgb(136, 136, 136);
               }
          }
     }

     .order_all_price {
          .all_num {
               font-size: 1.3rem
          }

          .all_num_price {
               font-size: 1.7rem;
               font-family: '数字专用';
          }

     }

     .aggregate {
          font-size: 1.3rem;
          margin-left: 15px;
     }

     .bill_clear {
          width: 12rem;
          height: 8.6rem;
          background-image: url("~@/common/image/orderImg/duiobianxi.png");
          background-size: cover;
          position: fixed;
          border-radius: 5px;
          top: 50%;
          left: 12px;
          z-index: 13;
          padding-top: 10px;
          span {
               display: inline-flex;
               align-items: center;
               justify-content: center;
               width: 100%;
               height: 50%;
               font-size: 1.3rem;
          }
     }

     .clear_order_alt_footer {
          width: 90%;
          background-color: #e65774;
          color: #ffffff;
          display: block;
          line-height: 44px;
          border-radius: 22px;
          overflow: hidden;
          font-size: 14px;
          position: fixed;
          bottom: 12px;
          display: flex;
          justify-content: space-between;
          left: 50%;
          transform: translateX(-50%);
     }

     .logistics {
          border: solid 1px #9d9d9d;
          color: #9d9d9d;
     }

     .show-logistics {
          width: 100vw;
          height: 100vh;
          position: fixed;
          top: 0;
          z-index: 1000;
     }

     iframe {
          width: 100%;
          height: 100%;
          border: none;
     }


     .apply_buyed {
          background-color: #f55056;
          border: none;
          color: #ffffff;
          margin-left: 1rem;
     }

     .comment_now {
          background-color: #f55056;
          border: none;
          color: #ffffff;
          margin-left: 1rem;
     }

     .clear_hiiden {
          opacity: 0;
     }

     .actve_header {
          color: #f55056 !important;
          font-weight: 600;
     }

     .group_bg_img {
          width: 29px;
          height: 29px;
          position: relative;
          overflow: hidden;
          background-size: cover;
          position: absolute;
          background-image: url("~@/common/image/detailsImg/yiwen@2x.png");

          img {
               width: 100%;
               position: absolute;
               top: 50%;
               left: 0;
               transform: translateY(-50%);
          }
     }

     .group_urlImg {
          display: flex;
          align-items: center;
          justify-content: flex-start;
          position: relative;
     }

     .group_active {
          border: solid 0.5px #f55056 !important;
          border-radius: 50% !important;
     }

     .group_invent {
          width: 12.5rem !important;
          background-color: #f55056;
          color: #ffffff;
          display: inline-block;
     }

     .fin_detail_group {
          width: 8.5rem;
          border: solid 1px #9d9d9d;
          color: #9d9d9d;
     }

     .order_zhanwei {
          background-image: url("~@/common/image/orderImg/zhanwei.png");
     }
     .pu_btn{
          display: flex;
          align-items: center;
          justify-content: flex-end;
     }

</style>
<template>
     <div id="order-content" ref="orderscro" class="order-content steep_padding" @scroll="scroll">
          <!--占位图-->
          <template v-if="skeleton">
               <div class="zhanwei order_zhanwei"></div>
          </template>
          <div class="show-logistics" v-if="showUrl">
               <iframe :src=logisticsUrl></iframe>
               <div @click="showUrl = false" style="position: absolute;top: 0;width: 7rem;height:5rem;z-index: 900;">
               </div>
          </div>
          <!--取消订单理由弹出-->
          <van-popup v-model="show" position="bottom" :style="{ height: '50%' }">
               <div>
                    <h3 style="text-align: center;font-size: 1.5rem;font-weight: 500;margin-bottom: 1rem;margin-top: 1.5rem">
                         取消订单</h3>
                    <!--<span style="margin-left: 1rem;font-size: 14px">请选择取消的原因</span>-->
                    <van-radio-group v-model="radio">
                         <van-cell-group>
                              <van-cell title="我不想买了" clickable @click="radio = '1'">
                                   <van-radio slot="right-icon" name="1" checked-color="#f55056"/>
                              </van-cell>
                              <van-cell title="信息填写错误，重新拍" clickable @click="radio = '2'">
                                   <van-radio slot="right-icon" name="2" checked-color="#f55056"/>
                              </van-cell>
                              <van-cell title="卖家缺货" clickable @click="radio = '3'">
                                   <van-radio slot="right-icon" name="3" checked-color="#f55056"/>
                              </van-cell>
                              <van-cell title="同城见面交易" clickable @click="radio = '4'">
                                   <van-radio slot="right-icon" name="4" checked-color="#f55056"/>
                              </van-cell>
                              <van-cell title="其他原因" clickable @click="radio = '5'">
                                   <van-radio slot="right-icon" name="5" checked-color="#f55056"/>
                              </van-cell>
                         </van-cell-group>
                    </van-radio-group>
                    <span class="clear_order_alt_footer">
                         <span style="width: 50%;text-align: center;background-color: #e71d4e"
                               @click="deleteOrder">确定取消</span>
                         <span style="width: 50%;text-align: center;background-color: #f55056" @click="show = false">暂不取消</span>
                    </span>
               </div>
          </van-popup>
          <div class="top_flex">
               <div class="order_header">
                    <HeaderPage>
                         <span slot="headerTitle">我的订单</span>
                    </HeaderPage>
               </div>
               <div class="table">
                    <ul>
                         <li :class="activeHeaderBol == index? 'actve_header' : ''"
                             @click="tableClick($event,item,index)"
                             v-for="(item,index) in tableLi"
                             :key="index + 'ins'"
                         >
                              {{ item }}
                         </li>
                    </ul>
               </div>
          </div>
          <van-pull-refresh style="padding-top: 9rem" v-model="isLoading" @refresh="onRefresh">
               <div class="allOrder">

                    <ul v-if="orderList.length != 0">
                         <li v-for="(item,index) in orderList" :key="index + 'ins'"
                             @click.stop="toOrderEetails(item.order_no)">
                              <!---->
                              <div class="order_title" v-if="item.goods_types != 'goods_group'">
                                   <div @click.stop="toShop(item.shop_id)" class="shop_info">
                                        <span>{{ item.shop_name }}</span>
                                        <img src="@/common/image/orderImg/right.png"
                                             style="width: 0.55rem;height: 1rem;"/>
                                   </div>
                                   <span v-if="item.order_status == 0">等待付款</span>
                                   <span v-if="item.order_status == 3">卖家已发货</span>
                                   <span v-if="item.order_status >= 4">已完成</span>
                                   <span v-if="item.order_status == 1">待发货</span>
                                   <span v-if="item.order_status == 2">已取消</span>
                              </div>
                              <!--拼团订单的头部-->
                              <div class="order_title group_order_num" v-if="item.goods_types == 'goods_group'">
                                   <div class="shop_info">
                                        <span>{{ item.shop_name }}</span>
                                        <img src="@/common/image/orderImg/right.png"
                                             style="width: 0.55rem;height: 1rem;"/>
                                   </div>
                                   <span v-if="item.shipping_status == 0">拼团成功，待发货</span>
                                   <span v-if="item.order_status == 3">拼团成功，已发货</span>
                                   <span v-if="item.period.info && item.shipping_status == -1 && item.order_status == 1">待分享，差{{ item.period.info.number - item.period.info.current }}人</span>
                                   <span v-if="item.order_status == 0">等待付款</span>
                                   <span v-if="item.order_status == 2">已取消</span>
                                   <span v-if="item.order_status >= 4">已完成</span>
                              </div>
                              <!---->
                              <h4>订单号:&nbsp&nbsp{{ item.order_no }}</h4>
                              <div class="com-content" v-for="(comInfo,comIndex) in item.goods"
                                   :key="comIndex + 'aser'">
                                   <div class="order_goods_img"
                                        :style="{backgroundImage:'url('+ comInfo.thumb +')'}"></div>
                                   <div class="order_goods_info">
                                        <div class="skuand_title">
                                             <p>{{ comInfo.title }}</p>
                                             <span>{{ comInfo.specs }}</span>
                                        </div>
                                        <div class="order_goods_price">
                                             <div class="goods_info_right_price">
                                                  <span style="font-size: 1rem">￥</span><span style="font-size: 1.7rem">{{ comInfo.goods_price.substring(0,comInfo.goods_price.length - 3) }}</span><span
                                                     style="font-size: 1rem">{{ comInfo.goods_price.substring(comInfo.goods_price.length - 3,comInfo.goods_price.length) }}</span>
                                             </div>
                                             <span class="buyHeader_price_num">x{{ comInfo.number }}</span>
                                        </div>
                                   </div>
                              </div>
                              <div class="order_all_price"
                                   style="display: flex;align-items: center;justify-content: flex-end;margin-top: 1.5rem">
                                   <span class="all_num">共{{ item.goods.length }}件商品</span>
                                   <span class="aggregate">总计</span>
                                   <div class="priceAdd">
                                        <span style="font-size: 1rem;font-family: '数字专用';">￥</span><span
                                           class="all_num_price">{{ item.pay_price.substring(0,item.pay_price.length - 3) }}</span><span
                                           style="font-size: 1rem;font-family: '数字专用';">{{ item.pay_price.substring(item.pay_price.length - 3,item.pay_price.length) }}</span>
                                   </div>
                              </div>
                              <div v-if="item.goods_types != 'goods_group'" class="li-footer">
                                   <template>
                                        <!-- 无用的占位div下面释放可以删除 -->
                                        <div></div>
                                        <!-- <div v-if="item.order_status >= 2 && item.order_status != 3" class="bill_alt" @click.stop="threeDianFun($event,item,index)">
                                             <img src="@/common/image/publicImg/threedian.png" alt="">
                                        </div> -->
                                   </template>
                                   <div class="pu_btn">
                                        <template v-if="item.goods_types.indexOf('goods_duobao') == -1">
                                             <span class="redSpan" v-if="item.order_status == 0"
                                                   @click.stop="toOrderEetails(item.order_no)">去付款</span>
                                        </template>
                                        <span class="comment_now" v-if="item.order_status == 4"
                                              @click.stop="comment(item.order_no,false)">立即评价</span>
                                        <span @click.stop="lengthen(item.order_no)" class="logistics"
                                              v-if="item.order_status == 3">延长收货</span>
                                        <span @click.stop="chakanwl(item)" class="logistics"
                                              v-if="item.order_status == 3">查看物流</span>
                                        <span v-if="item.order_status == 3"
                                              @click.stop="showOrder(item.order_no,index)" class="redSpan"
                                              style="color: #ffffff">确认收货</span>
                                        <span @click.stop="remind" class="redSpan"
                                              v-if="item.order_status == 1">提醒发货</span>
                                        <span  @click.stop="clearOrder(item,index)"  v-if="item.order_status == 2" class="logistics">删除订单</span>
                                   </div>
                              </div>
                              <!--拼团的按钮-->
                              <div v-if="item.goods_types == 'goods_group'" class="li-footer">
                                   <div class="group_urlImg">
                                        <template v-if="item.period.info">
                                             <div
                                                     v-for="(niceImg,niceins) in item.period.info.number"
                                                     :style="{
                                                     left:(niceins * 20) + 'px',
                                                     zIndex:item.period.info.number - niceins,
                                                     backgroundImage:item.period.lists[niceins]? 'url(' + item.period.lists[niceins].headimgurl +')' : ''
                                                     }"
                                                     :class="item.period.lists[niceins]? 'group_active' : ''"
                                                     class="group_bg_img"
                                                     :key="niceins + 'cjks'"
                                             >
                                             </div>
                                        </template>
                                   </div>
                                   <div>
                                        <span class="comment_now" v-if="item.order_status == 4"
                                              @click.stop="comment(item.order_no,false)">立即评价</span>
                                        <span @click.stop="lengthen(item.order_no)" class="logistics"
                                              v-if="item.order_status == 3">延长收货</span>
                                        <span @click.stop="chakanwl(item)" class="logistics"
                                              v-if="item.order_status == 3">查看物流</span>
                                        <span v-if="item.order_status == 3"
                                              @click.stop="showOrder(item.order_no,index)" class="redSpan"
                                              style="color: #ffffff">确认收货</span>
                                        <span @click.stop="shareTeam(item)" class="group_invent" v-if="item.order_status == 1 && item.shipping_status == -1">邀请好友拼单</span>
                                        <span @click.stop="remind" class="redSpan"
                                              v-if="item.order_status == 1 && item.shipping_status == 0">提醒发货</span>
                                        <span @click.stop="clearOrder(item,index)"  v-if="item.order_status == 2" class="logistics">删除订单</span>
                                   </div>
                              </div>
                         </li>
                    </ul>

               </div>
          </van-pull-refresh>
          <!--开具发票删除订单的遮罩-->
          <van-overlay z-index="12" class-name="clear_hiiden" :show="opacityhid" duration="0"
                       @click="opacityhid = false">
          </van-overlay>
          <div v-if="opacityhid" class="bill_clear" :style="{'top':top + 'px'}">
               <span @click="toBill" v-if="threeDianInfo.order_status >= 4">开具发票</span>
               <span @click="clearOrder" v-if="threeDianInfo.order_status == 2 || threeDianInfo.order_status == 4 || threeDianInfo.order_status == 5">删除订单</span>
          </div>
     </div>
</template>

<script>
     import {Dialog} from 'vant';
     import storage from "@/common/js/storage.js"
     import HeaderPage from "../components/HeaderPage"

     export default {
          name: "OrderPage",
          data() {
               return {
                    opacityhid: false,
                    showOk: false,
                    orderList: [],
                    tableLi: ['全部', '待付款', '待分享', '待发货', '待收货', '已完成'],
                    clearOrderBool: true,
                    radio: '-1',
                    show: false,
                    order_no: null,
                    shoreComBool: true,
                    order_status: '0,1,2,3,4',
                    top: 100,
                    logisticsUrl: '',
                    showUrl: false,
                    activeHeaderBol: 0,
                    page: 1,
                    goods_type: "",
                    moreBool: true,
                    userInfo: null,
                    skeleton: true,
                    scrollTop: 0,
                    shippingStatus: -2,
                    isLoading: false,
                    threeDianInfo:null,
                    threeDianIndex:-1,
                    scrollBool:true
               }
          },
          methods: {
               //到店铺页面
               toShop(shopId){
                    this.$router.push({name:"shop",query:{shop_id:shopId}})
               },
               // 到开具发票
               toBill(){
                    console.log(this.threeDianInfo)
                    this.$router.push({
                         name:"bill",
                         params:{
                              orderInfo:this.threeDianInfo
                         }
                    })
               },
               showOrder(order_no,index) {
                    Dialog.confirm({
                         title: '确认收货',
                         message: '确认已经收到货物，避免财货两空!',
                    }).then(() => {
                         this.affirm(order_no,index)
                    }).catch(() => {
                         // on cancel
                    });
               },
               onRefresh() {
                    setTimeout(() => {
                         this.$toast('刷新成功');
                         this.isLoading = false;
                         this.page = 1
                         this.getOrderList((data) => {
                              this.orderList = data
                         })
                    }, 500);
               },
               //删除无用的订单
               clearOrder(val,index) {
                    Dialog.confirm({
                         title: '删除订单',
                         message: '确认要删除该订单吗'
                    }).then(() => {
                         if(val){
                              var obj = {
                                   order_no:val.order_no
                              }
                         }else{
                              var obj = {
                              order_no:this.threeDianInfo.order_no
                         }
                         }
                         this.postObj(obj, window.PATHS.ORDER_UDELORDER, (result) => {
                              if (result.code == 1) {
                                   if(index){
                                        this.orderList.splice(index, 1)
                                        this.opacityhid = false
                                   }else{
                                        this.orderList.splice(this.threeDianIndex, 1)
                                        this.opacityhid = false
                                   }

                              }
                         })
                    }).catch(() => {
                         // on cancel
                         this.opacityhid = false
                    });

               },
               //邀请好友拼单
               shareTeam(item) {
                    console.log(item)
                    var obj = {
                         title: item.goods[0].title,
                         content: "拼团优惠",
                         url: window.HOST + "/goods/group?uid=" + this.userInfo.id + "&goods_id=" + item.goods[0].goods_id + "&period_id=" + item.goods[0].period_id,
                         imgsrc: item.goods[0].thumb,
                         nickname: this.userInfo.nickname,
                         headimgurl: this.userInfo.headimgurl,
                    }
                    this.getComInfo(item.goods[0].goods_id, (data) => {
                         if (data) {
                              obj.shop_price = data.shop_price,
                                  obj.market_price = data.market_price,
                                  obj.sales = data.sales,
                                  obj.needPersor = data.extra_data.number - data.extra_data.current,
                                  obj.duration = data.extra_data.duration,
                                  obj.extra_type = data.extra_table
                              console.log(obj)
                              this.shareSame(obj)
                         }
                    })
               },
               //延长收货
               lengthen(order_no) {
                    Dialog.confirm({
                         message: "确认延长收货时间？每笔订单</br>只能延长一次哦",
                         confirmButtonColor: "#f55056",
                         cancelButtonColor: "#9d9d9d"
                    }).then(() => {
                         var obj = {
                              order_no
                         }
                         this.postObj(obj, window.PATHS.SHOPSORDER_CONFIRMDEFER, (result) => {
                              if (result.code == 1) {
                                   this.$toast.center("延长收货成功")
                              }
                         })
                    }).catch(() => {
                         // on cancel
                    });
               },
               //查看物流
               chakanwl(orderInfo) {
                    this.logisticsUrl = orderInfo.logistics_url
                    this.showUrl = true
               },
               //提醒发货
               remind() {
                    Dialog.alert({
                         confirmButtonColor: "#f55056",
                         message: '卖家已收到你的提醒，会尽快发货'
                    }).then(() => {
                         // on close
                    });
               },
               threeDianFun(ev,item,index) {
                    if (ev.clientY >= window.innerHeight - window.innerHeight/5){
                         $(this.$refs.orderscro).animate({
                              scrollTop:this.scrollTop + window.innerHeight/5
                         },300)
                         this.top = ev.clientY - window.innerHeight/5 + 9
                         setTimeout(() => {
                              this.opacityhid = true
                         },300)
                    }else{
                         this.top = ev.clientY + 9
                         this.opacityhid = true
                    }
                    this.threeDianInfo = item
                    this.threeDianIndex = index

               },
               //再来一单
               // ageinOrder(order_no){
               //      this.$store.commit("setagainOrder",order_no)
               //      location.href = "#/againorder"
               // },
               //立即评价与追评
               comment(order_no, bool) {
                    this.postObj({order_no}, window.PATHS.SHOPS_ORDER_GET_INFO, (result) => {
                         if (result.code == 1) {
                              if (result.data.goods.length > 1) {
                                   this.$store.commit('setCommentOrder', order_no)
                                   location.href = '#/comment'
                              } else {
                                   if (bool) {
                                        this.$router.push({
                                             name: 'commenteeild',
                                             params: {comInfo: result.data.goods[0], order_no, bool}
                                        })
                                   } else {
                                        this.$router.push({
                                             name: 'commenteeild',
                                             params: {comInfo: result.data.goods[0], order_no}
                                        })
                                   }
                              }
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //再看看
               lookMore() {
                    this.showOk = false
                    this.getOrderList(this.radio)
               },
               //确认收货
               affirm(order_no,index) {
                    var that = this
                    if (this.shoreComBool) {
                         this.shoreComBool = false
                         this.postObj({order_no}, window.PATHS.ORDER_SHIOOINGORDER, (result) => {
                              setTimeout(() => {
                                   that.shoreComBool = true
                              }, 1000)
                              if (result.code == 1) {
                              //     收货成功
                                   this.$toast.center("收货成功")
                                   setTimeout(() => {
                                        that.$router.push({name: "rderetails", query: {order_no}})
                                   },1000)
                                   setTimeout(() => {
                                        this.orderList.splice(index,1)
                                   },1500)
                              }
                         })
                    }
               },
               //取消订单
               deleteOrder() {
                    var that = this
                    if (this.clearOrderBool === true) {
                         // this.clearOrderBool = false
                         var obj = {}
                         obj.order_no = this.order_no

                         if (this.radio != "-1") {
                              if (this.radio == "1") {
                                   obj.remark = "我不想买了"
                              }
                              if (this.radio == "2") {
                                   obj.remark = "信息填写错误重新拍"
                              }
                              if (this.radio == "3") {
                                   obj.remark = "卖家缺货"
                              }
                              if (this.radio == "4") {
                                   obj.remark = "同城见面交易"
                              }
                              if (this.radio == "5") {
                                   obj.remark = "其他原因"
                              }
                         }

                         this.postObj(obj, window.PATHS.ORDER_CANCELORDER, (result) => {
                              console.log(result)
                              this.show = false
                              setTimeout(() => {
                                   that.clearOrderBool = true
                              }, 1500)
                              if (result.code == 1) {
                                   this.$toast.center('取消成功')
                                   this.orderList = []
                                   this.getOrderList()
                              } else {
                                   this.$toast.center(result.info)
                              }
                         })
                    }
               },
               // 获取订单列表
               getOrderList(callBack) {
                    var that = this
                    var obj = {
                         order_type: "shop_goods",
                         order_status: this.order_status,
                         page: this.page,
                         shipping_status: this.shippingStatus
                    }
                    if (this.goods_type != "") {
                         obj.goods_type = this.goods_type
                    }
                    this.postObj(obj, window.PATHS.SHOPS_ORDER_GET_ORDER_LIST, (result) => {
                         if (result.code == 1) {
                              this.userInfo = result.user
                              this.skeleton = false
                              if (callBack){
                                   callBack(result.data)
                              } else{
                                   if (result.data.length != 0) {
                                        this.orderList = this.orderList.concat(result.data)
                                        setTimeout(() => {
                                             that.scrollBool = true
                                        }, 500)
                                   }else{
                                       this.moreBool = false
                                   }
                              }
                         }
                    })
               },
               //     点击头部变化订单列表
               tableClick(ev, tabTxt, index) {
                    this.shippingStatus = -2
                    this.orderList = []
                    this.page = 1
                    this.activeHeaderBol = index
                    if (tabTxt == "全部") {
                         this.order_status = '0,1,2,3,4'
                         this.goods_type = ''
                         this.getOrderList()
                    }
                    if (tabTxt == "待付款") {
                         this.order_status = '0'
                         this.goods_type = ''
                         this.getOrderList()
                    }
                    if (tabTxt == "待分享") {
                         this.order_status = '1'
                         this.goods_type = 'goods_group'
                         this.shippingStatus = -1
                         this.getOrderList()
                    }
                    if (tabTxt == "待发货") {
                         this.order_status = '1'
                         this.goods_type = ''
                         this.shippingStatus = 0
                         this.getOrderList()
                    }
                    if (tabTxt == "待收货") {
                         this.order_status = '3'
                         this.goods_type = ''
                         this.getOrderList()
                    }
                    if (tabTxt == "已完成") {
                         this.order_status = '4'
                         this.goods_type = ''
                         this.getOrderList()
                    }
               },
               //跳转到订单详情
               toOrderEetails(order_no) {
                    this.$router.push({name: "rderetails", query: {order_no}})
               },
               //调取商品详情
               getComInfo(goods_id, callback) {
                    var obj = {
                         goods_id
                    }
                    this.postObj(obj, window.PATHS.SHOPS_GOODS_GET_INFO, (result) => {
                         if (result.code == 1) {
                              callback(result.data)
                         } else {
                              callback(false)
                         }
                    })
               },
               scroll() {
                    this.scrollTop = this.$refs.orderscro.scrollTop
                    var height = this.$refs.orderscro.scrollHeight
                    var windowHeight = window.innerHeight
                    this.scrollMore(this.scrollTop,height,windowHeight,() => {
                          if (this.scrollBool) {
                              this.scrollBool = false
                              if(this.moreBool){
                                   this.page++
                                   this.getOrderList()
                              }
                         }
                    })
                    // if (this.scrollTop <= 0) {
                    //      this.$refs.orderscro.scrollTop = 0
                    // }
               },
          },
          mounted() {
          },
          created() {
               //使用原生传递过来的order_status
               if (this.$route.query.order_status) {
                    this.order_status = this.$route.query.order_status
                    this.activeHeaderBol = this.$route.query.headerType
                    this.shippingStatus = -2
                    if(this.order_status == "1"){
                         this.shippingStatus = 0
                    }
                    if(this.$route.query.shippingStatus){
                         this.$toast.center("执行了")
                         this.shippingStatus = -1
                    }
                    if(this.order_status == '-1'){
                         this.order_status = "0,1,2,3,4"
                    }
                    this.getOrderList()
               } else {
                    this.getOrderList()
               }
          },
          activated() {
               this.$refs.orderscro.scrollTop = this.scrollTop
               if (storage.get("orderPageRefaus")){
                    this.page = 1
                    this.getOrderList((data) => {
                         this.orderList = data
                    })
               }
          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to, from, next) {
               Dialog.close()
               storage.remove("orderPageRefaus")
               next()
          },
          beforeRouteEnter(to, from, next) {
               next()
          }
     }
</script>




