
import {Dialog} from "vant";

//加入购物车
export const joinShoppingCar = (that, newObj, callBack) => {
     //newObj中的数据

     // newObj.skuList             当前商品的sku列表(必填)
     // newObj.skugroup            当前商品的sku大类列表(必填)
     // newObj.chooseSkuList       当前已经选中的规格数组(必填)
     // newObj.goods_id            当前商品id(必填)
     // newObj.number              加入购物车的数量(必填)
     // newObj.video_id            可选(视频id)(可选)
     // newObj.share_user_id       可选(分享人ID/VLOG带货主播ID)(可选)
     // extra_table                 商品类型用来判断是否能加入购物车(可选)


     //海外购不许加入购物车
     if (newObj.extra_table == "goods_customs"){
          Dialog.alert({
               title: '提示',
               message: '海外购商品暂不支持加入购物车，您可将商品添加至收藏，或者直接下单',
               confirmButtonColor:"#f55056"
          }).then(() => {
               callBack()
          });
          return
     }
     var chieck = 1
     var title = []
     //请选择规格
     newObj.skugroup.forEach((item, index) => {
          var index2 = newObj.chooseSkuList[index]
          if (index2 == undefined) {
               if (chieck == 1) {
                    that.$toast.center('请选择' + item.title)
               }
               chieck = 0;
               return
          }
     })
     if (chieck == 0) {
          return
     }
     var obj = {
          goods_id: newObj.goods_id,
          number: newObj.number,
          video_id: newObj.video_id? newObj.video_id : 0,
          share_user_id:newObj.share_user_id? newObj.share_user_id : 0,
     }
     var skuStr = newObj.chooseSkuList.join(",")
     newObj.skuList.forEach((item) => {
          if (item.title == skuStr) {
               obj.sku_path = item.path
          }
     })
     that.$loading()
     that.postObj(obj, window.PATHS.shoppingCar.SHOPSGOODSCART_ADDGOODS, (result) => {
          if (result.code == 1) {
               that.$loading.close()
               that.$toast.center("已加入购物车")
               callBack()
          }
     })
}




