<style scoped lang="scss">
     #biil_page {
          height: 100vh;
          background-color: #f6f6f6;
          overflow-y: scroll;
     }

     .bill_page_header {
          position: fixed;
          width: 100%;
          background-color: #ffffff;
          top: 0;
          left: 0;

          > div {
               position: relative;
          }

          .bill_guize {
               position: absolute;
               right: 1.2rem;
               top: 50%;
               transform: translateY(-50%);
               font-size: 1.1rem;
               a{
                    color: #f55056;
               }
          }
     }

     .bill_content {
          padding: 0 1.2rem;
          padding-top: 4.4rem;
     }

     .same_div {
          background-color: #ffffff;
          padding: 1.25rem;
          border-radius: 0.7rem;
          margin-top: 0.7rem;
     }

     .order_goods_info {
          font-size: 1.4rem;

          .price_order {
               margin-top: 1.6rem;

               .num_family {
                    color: #f55056;
               }
          }
     }

     .bill_inpt {
          ul {
               li:first-child {
                    margin-top: 0;
               }

               li {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-top: 2.5rem;

                    h5 {
                         font-size: 1.5rem;
                    }

                    .inpu_li_right_span {
                         font-size: 1.2rem;
                    }

                    .bill_type {
                         display: flex;
                         align-items: center;
                         justify-content: flex-end;
                    }

                    input {
                         border: none;
                         font-size: 1.2rem;
                         text-align: right;
                    }
               }
          }
     }

     .bill_mingx {
          display: flex;
          align-items: center;
          justify-content: space-between;

          h5 {
               font-size: 1.5rem;
          }

          span {
               font-size: 1.2rem;
          }
     }

     .conent_for_more {
          text-align: center;
          margin-top: 2rem;

          img {
               width: 1.9rem;
               height: 1.1rem;
          }
     }

     .footer_submi {
          padding: 0.6rem 1.8rem;
          background-color: #ffffff;
          position: fixed;
          bottom: 0;
          width: 100%;

          span {
               line-height: 3.8rem;
               text-align: center;
               display: inline-block;
               background-color: #f55056;
               border-radius: 1.9rem;
               width: 100%;
               font-size: 1.4rem;
               color: #ffffff;
          }
     }
</style>

<template>
     <div id="biil_page" class="steep_padding">
          <div class="bill_page_header">
               <div>
                    <HeaderPage>
                         <span slot="headerTitle">申请开票</span>
                         <span slot="headerRight" class="bill_guize"><a href="#/billrules">发票规则</a></span>
                    </HeaderPage>
               </div>
          </div>
          <div class="bill_content">
               <div class="order_goods_info same_div">
                    <div>订单编号:{{ orderInfo.order_no }}</div>
                    <div class="price_order">
                         <span>开票金额:</span>
                         <span class="num_family" style="font-size:1.1rem">￥</span>
                         <span
                                 class="num_family"
                         >{{ orderInfo.order_price.substring(0,orderInfo.order_price.length - 3) }}</span>
                         <span
                                 class="num_family"
                                 style="font-size:1.1rem"
                         >{{ orderInfo.order_price.substring(orderInfo.order_price.length - 3,orderInfo.order_price.length) }}</span>
                    </div>
               </div>
               <div class="same_div bill_inpt">
                    <ul>
                         <li>
                              <h5>发票类型</h5>
                              <span class="inpu_li_right_span">电子发票</span>
                         </li>
                         <li>
                              <h5>抬头类型</h5>

                              <van-radio-group v-model="radioType" icon-size="1.2rem" checked-color="#f55056">
                                   <div class="bill_type inpu_li_right_span">
                                        <van-radio name="个人">个人</van-radio>
                                        <van-radio style="margin-left:2.5rem" name="单位">单位</van-radio>
                                   </div>
                              </van-radio-group>
                         </li>
                         <li>
                              <h5>手机号</h5>
                              <input type="number" v-model="mobile" placeholder="请输入正确格式的手机号"/>
                         </li>
                         <li>
                              <h5>邮箱</h5>
                              <input type="email" v-model="email" placeholder="请输入邮箱地址"/>
                         </li>
                         <li v-if="radioType == '单位'">
                              <h5>税号</h5>
                              <input type="text" v-model="duty" placeholder="税号"/>
                         </li>

                         <li v-if="radioType == '单位'">
                              <h5>单位名称</h5>
                              <input type="text" v-model="unitName" placeholder="单位名称"/>
                         </li>

                         <template v-if="radioType == '单位' && moreBol">
                              <li>
                                   <h5>开户银行</h5>
                                   <input type="text" v-model="bank_name" placeholder="可选"/>
                              </li>
                              <li>
                                   <h5>银行账号</h5>
                                   <input style="width: 200px;" type="text" v-model="bank_no" placeholder="可选" :maxlength="23"/>
                              </li>
                              <li>
                                   <h5>企业地址</h5>
                                   <input type="number" v-model="address" placeholder="可选"/>
                              </li>
                              <li>
                                   <h5>电话</h5>
                                   <input type="number" v-model="phone" placeholder="公司电话"/>
                              </li>
                         </template>
                    </ul>
                    <div v-if="radioType == '单位'" class="conent_for_more" @click="moreBol = !moreBol">
                         <img v-if="!moreBol" src="@/common/image/bill/huiRight.png"/>
                         <img v-if="moreBol" src="@/common/image/bill/huiRightBtn.png"/>
                    </div>
               </div>
               <div class="bill_mingx same_div">
                    <h5>发票内容</h5>
                    <span>明细</span>
               </div>
          </div>
          <div class="footer_submi">
               <span @click="submitObj">提交申请</span>
          </div>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage";
     import {Dialog} from "vant";

     export default {
          data() {
               return {
                    orderInfo: null,
                    radioType: "个人",
                    moreBol: false,
                    mobile: "",
                    email: "",
                    unitName:"",
                    duty:"",
                    bank_name:'',
                    bank_no:"",
                    address:"",
                    phone:"",
                    rules:false
               };
          },
          methods: {
               // 提交申请
               submitObj() {
                    var obj = {
                         order_no: this.orderInfo.order_no
                    };
                    // 手机号验证
                    if (this.mobile != "") {
                         var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
                         if (!myreg.test(this.mobile)) {
                              this.$toast.center("请输入正确的手机号");
                              return;
                         } else {
                              obj.mobile = this.mobile;
                         }
                    } else {
                         this.$toast.center("请输入手机号");
                         return
                    }
                    // 邮箱验证
                    if (this.email != "") {
                         var reg = new RegExp(
                             "^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"
                         );
                         if (reg.test(this.email)) {
                              obj.email = this.email;
                         } else {
                              this.$toast.center("请输入正确的邮箱");
                              return;
                         }
                    }
                    // 个人提交
                    if (this.radioType == "个人") {
                         obj.name = "个人";
                    }
                    //公司提交
                    if (this.radioType == "单位"){
                         if (this.duty != ''){
                              obj.taxpayer_id = this.duty
                         }else{
                              this.$toast.center("请输入纳税人识别码")
                              return
                         }
                         if (this.unitName != ''){
                              obj.name = this.unitName
                         } else{
                              this.$toast.center("请输入单位名称")
                              return
                         }
                         //次要
                         if (this.bank_name != ''){
                              obj.bank_name = this.bank_name
                              if (this.bank_no == ''){
                                   this.$toast.center("请输入银行卡号")
                                   return
                              }
                         }
                         if (this.bank_no != ''){
                              var bank_new = this.bank_no.replace(/\s*/g, '');
                              var regExp = /^([1-9]{1})(\d{15}|\d{18})$/;
                              if (regExp.test(bank_new)){
                                   obj.bank_no = bank_new
                                   if (this.bank_name == ''){
                                        this.$toast.center("请输开户行名称")
                                   }
                              }else{
                                   this.$toast.center("银行卡号格式不正确")
                                   return
                              }

                         }
                         if (this.address != ''){
                              obj.address = this.address
                         }
                         if (this.phone != ""){
                              var phoneReg = /^([0-9]{3,4}-)?[0-9]{7,8}$/;
                              if (phoneReg.test(this.phone)) {
                                   obj.phone = this.phone
                              }else{
                                   this.$toast.center("电话格式有误")
                                   return
                              }
                         }
                    }
                    console.log(obj)
                    this.postObj(obj,window.PATHS.BILL.SHOPSORDER_ADDINVOICE,(result) => {
                         if (result.code == 1){
                              this.$toast.center("申请成功")
                              setTimeout(() => {
                                   window.history.go(-1)
                              },2000)
                         }
                    })
               }
          },
          created() {
               this.orderInfo = this.$route.params.orderInfo;
          },
          activated(){
               if (this.$route.meta.keepLoadBool){
                    this.orderInfo = this.$route.params.orderInfo;
               }
          },
          components: {
               HeaderPage
          },
          watch: {
               radioType(newval, oldval) {
                    if (newval == "个人") {
                         this.moreBol = false;
                    }
               },
               ["bank_no"](newval){
                    // 四位空一格
                    this.$nextTick(() => {
                         this.bank_no = newval.replace(/\s/g,'').replace(/....(?!$)/g,'$& ');
                    });
               }
          },
          beforeRouteLeave(to,from,next){
               if (to.name == "order"){
                         from.meta.keepLoadBool = true
                         this.orderInfo = null,
                        this.radioType = "个人",
                        this.moreBol = false,
                        this.mobile = "",
                        this.email = "",
                        this.unitName = "",
                        this.duty = "",
                        this.bank_name = '',
                        this.bank_no = "",
                        this.address = "",
                        this.phone = "",
                        this.rules = false
               } else{
                    from.meta.keepLoadBool = false
               }
               next()
          }
     };
</script>





