
<style scoped>
     .agree > span{
          font-size: 13px;
          color: #f55056;
     }
     .agree{
          display: flex;
          align-items: center;
     }
     .cant_now{
          background-color: rgba(245, 80, 86, 0.51);
     }
     .apply_now{
          background-color: #f55056;
     }
     .vlog_footer > span{
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          height: 4rem;
          color: #ffffff;
          border-radius: 2rem;
          font-size: 1.7rem;
          margin-top: 16px;
     }
     .vlog_footer{
          width: 93.6%;
          position: fixed;
          bottom: 16px;
          left: 50%;
          transform: translateX(-50%);
     }
     .forward_require ul li{
          margin-bottom: 13px;
          font-size: 1.3rem;
     }
     h5{
          font-size: 1.1rem;
          margin: 15.5px 0;
     }
     .get_name{
          font-size: 1.1rem;
          color: #ffffff;
          width: 6rem;
          height: 2.9rem;
          border-radius: 1.45rem;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: #f55056;
     }
     .was_get_name{
          font-size: 1.1rem;
          color: #9d9d9d;
     }
     .name_get span{
          margin-left: 16px;
          font-size: 1.5rem;
     }
     .name_get {
          display: flex;align-items: center;
     }
     .base_require img{
          width: 4rem;
          height: 4rem;
     }
     .base_require > div{
          background-color: #efefef;
          padding: 10px 13px;
          display: flex;
          justify-content: space-between;
          align-items: center;
     }
     .vlog_content > img{
          margin-bottom: 22px;
          width: 100%;
     }
     .vlog_content{
          width: 93.6%;
          margin: 0 auto;
          padding-top: 5.4rem;
     }
     .vlog_header{
          position: relative;
          box-shadow: 0 2px 3px 0 #f4f4f4;
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          background-color: #ffffff;
     }
     #vlogPage{
          padding-top: 5rem;
     }
</style>

<template>
     <!--种草-->
     <div id="vlogPage" class="steep_padding">
          <div class="vlog_header">
               <HeaderPage>
                    <span slot="headerTitle">种草达人申请</span>
               </HeaderPage>
          </div>
          <div class="vlog_content">
               <div class="base_require">
                    <h5>基础要求:完成实名认证</h5>
                    <div>
                         <div class="name_get">
                              <img src="@/common/image/vlogimg/2fea506bbbfa58c5cc1d40846a3074ce@2x.png" alt="">
                              <span>实名认证</span>
                         </div>
                         <div v-if="statistics != null">
                              <span v-if="statistics.user_auth == 1" class="was_get_name">已认证</span>
                              <span @click="auth" v-if="statistics.user_auth == 0" class="get_name">去认证</span>
                         </div>
                    </div>
               </div>
               <div class="forward_require">
                    <h5>进阶要求</h5>
                    <ul>
                         <li>
                              1、个人主页视频(公开且审核通过)≥5条
                         </li>
                         <li>
                              2、账号粉丝量≥300
                         </li>
                    </ul>
               </div>
               <van-divider />
               <div class="forward_require">
                    <h5>获得权益:</h5>
                    <ul>
                         <li>
                              1、拥有个人主页商品橱窗功能
                         </li>
                         <li>
                              2、拥有个人主页视频置顶功能
                         </li>
                         <li>
                              3、Vlog功能
                         </li>
                    </ul>
               </div>
               <van-divider />
          </div>
          <div class="vlog_footer">
               <div class="agree">
                    <van-checkbox checked-color="#f55056" v-model="checked"><span style="font-size: 13px">我已阅读并同意</span></van-checkbox>
                    <span>《种草达人视频分享协议》</span>
               </div>
               <template>
                    <span v-if="checked && statistics.is_up == 1" class="apply_now">立即申请</span>
                    <span v-else class="cant_now">立即申请</span>
               </template>
          </div>
     </div>
</template>
<script>
     import HeaderPage from "@/components/HeaderPage"
     export default {
          name: "VlogPage",
          data(){
               return {
                    checked:false,
                    statistics:null,
                    isCanBool:false
               }
          },
          methods:{
               //返回上一页
               goBack(){
                    window.history.go(-1)
               },
               //去认证
               auth(){
                    location.href = "app://auth/index"
               },
          //     获取我是否够种草带货的资格
               maleCanInfo(){
                    var obj = {}
                    this.postObj(obj,window.PATHS.VLOG.VLOG_GETINFO,(result) => {
                         if (result.code == 1){
                              this.statistics = result.data
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               }
          },
          mounted(){
               var mo=function(e){e.preventDefault();}
               document.addEventListener("touchmove",mo,false)
          },
          created(){
               this.maleCanInfo()
          },
          components:{
               HeaderPage
          }
     }
</script>




