<style scoped lang="scss">
     #after_history {
          height: 100vh;
          overflow-y: scroll;
          background-color: #f6f6f6;
          padding-top: 4.4rem;
     }

     .header {
          width: 100%;
          background-color: #ffffff;
          position: fixed;
          top: 0;
          left: 0;
          box-shadow: 0 2px 5px 0 #d6d6d6;
     }

     .same_div {
          padding: 20px 12px;
          background-color: #ffffff;
          border-radius: 7px;
          margin-bottom: 7px;

          .name_title {
               display: flex;
               align-items: center;
               justify-content: flex-start;

               .step_num {
                    display: inline-block;
                    width: 2.5rem;
                    line-height: 2.5rem;
                    border-radius: 50%;
                    text-align: center;
                    color: #ffffff;
                    font-size: 1.5rem;
                    background-color: #dddddd;
               }

               .name_title_name_time {
                    margin-left: 10px;

                    h4 {
                         font-size: 1.5rem;
                    }

                    span {
                         font-size: 1.1rem;
                         color: #9d9d9d;
                    }
               }
          }
     }

     .content_after {
          margin-top: 25px;

          p {
               font-size: 1.3rem;
               margin-bottom: 3px;
          }
     }
     .cexiao{
          font-size: 1.3rem;
     }
</style>


<template>
     <!--协商历史-->
     <div id="after_history" v-if="afterInfo">
          <div class="header">
               <HeaderPage>协商历史</HeaderPage>
          </div>
         <div v-if="afterInfo.status == 5" class="same_div">
              <h4 class="cexiao">该售后已撤销</h4>
         </div>
          <!--仅仅退款-->
          <template v-if="afterInfo.refund_type == '1'">
               <div class="same_div">
                    <div class="name_title">
                         <div class="step_num num_family">2</div>
                         <div class="name_title_name_time">
                              <h4>{{ afterInfo.shop_name }}</h4>
                              <span>{{ afterInfo.check_time }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <p v-if="afterInfo.check_status == 1">商家同意退款申请，请注意退款到账</p>
                         <template v-if="afterInfo.check_status == 2">
                              <p>商家拒绝拒绝您的退款申请</p>
                              <p>拒绝原因:{{ afterInfo.replay }}</p>
                         </template>
                    </div>
               </div>
               <div class="same_div">
                    <div class="name_title">
                         <div class="step_num num_family">1</div>
                         <div class="name_title_name_time">
                              <h4>{{ userInfo.nickname }}</h4>
                              <span>{{ firstTime }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <p>售后类型:仅退款</p>
                         <p>退款原因:{{ afterInfo.reason }}</p>
                         <p>退款金额:￥{{ afterInfo.refund_price }}</p>
                         <p v-if="afterInfo.content">问题描述:{{ afterInfo.content }}</p>
                    </div>
               </div>
          </template>
          <!--退款退货-->
          <template v-if="afterInfo.refund_type == '2'">
               <div class="same_div" v-if="afterInfo.end_time">
                    <div class="name_title">
                         <div class="step_num num_family">4</div>
                         <div class="name_title_name_time">
                              <h4>{{ afterInfo.shop_name }}</h4>
                              <span>{{ afterInfo.end_time }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <p>卖家已收到货物，请留意退款到账，</p>
                    </div>
               </div>
               <div class="same_div" v-if="afterInfo.logistics_time">
                    <div class="name_title">
                         <div class="step_num num_family">3</div>
                         <div class="name_title_name_time">
                              <h4>{{ userInfo.nickname }}</h4>
                              <span>{{ afterInfo.logistics_time }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                        <p>您已将货物发给买家，请等待买家确认收到货物后，将退款退回到您的账户</p>
                    </div>
               </div>
               <div class="same_div" v-if="afterInfo.check_time">
                    <div class="name_title">
                         <div class="step_num num_family">2</div>
                         <div class="name_title_name_time">
                              <h4>{{ afterInfo.shop_name }}</h4>
                              <span>{{ afterInfo.check_time }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <template v-if="afterInfo.check_status == 1">
                              <p>商家已同意您的退款退货，请买家尽快把货物寄回</p>
                              <p>退货地址: {{ afterInfo.sender_name }},{{ afterInfo.sender_phone }},{{ afterInfo.sender_address }}</p>
                         </template>
                         <template v-if="afterInfo.check_status == 2">
                              <p>商家拒绝了您的退货退款</p>
                              <p>原因:{{ afterInfo.replay }}</p>
                         </template>
                    </div>
               </div>
               <div class="same_div">
                    <div class="name_title">
                         <div class="step_num num_family">1</div>
                         <div class="name_title_name_time">
                              <h4>{{ userInfo.nickname }}</h4>
                              <span>{{ firstTime }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <p>售后类型:退款退货</p>
                         <p>退货原因:{{ afterInfo.reason }}</p>
                         <p>退款金额:￥{{ afterInfo.refund_price }}</p>
                         <p v-if="afterInfo.content">问题描述:{{ afterInfo.content }}</p>
                    </div>
               </div>

          </template>
          <!--退款退货-->
          <template v-if="afterInfo.refund_type == '3'">
               <div class="same_div" v-if="afterInfo.end_time">
                    <div class="name_title">
                         <div class="step_num num_family">4</div>
                         <div class="name_title_name_time">
                              <h4>{{ afterInfo.shop_name }}</h4>
                              <span>{{ afterInfo.end_time }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <p>卖家已收到货物，等待新的货物发送至您的手上</p>
                    </div>
               </div>
               <div class="same_div" v-if="afterInfo.logistics_time">
                    <div class="name_title">
                         <div class="step_num num_family">3</div>
                         <div class="name_title_name_time">
                              <h4>用户姓名</h4>
                              <span>{{ afterInfo.logistics_time }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <p>您已将货物发给卖家，请等待卖家确认</p>
                    </div>
               </div>
               <div class="same_div" v-if="afterInfo.check_time">
                    <div class="name_title">
                         <div class="step_num num_family">2</div>
                         <div class="name_title_name_time">
                              <h4>{{ afterInfo.shop_name }}</h4>
                              <span>{{ afterInfo.check_time }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <template v-if="afterInfo.check_status == 1">
                              <p>商家已同意您的换货，请买家尽快把货物寄回</p>
                              <p>退货地址: {{ afterInfo.sender_name }},{{ afterInfo.sender_phone }},{{ afterInfo.sender_address }}</p>
                         </template>
                         <template v-if="afterInfo.check_status == 2">
                              <p>商家拒绝了您的换货</p>
                              <p>原因:{{ afterInfo.replay }}</p>
                         </template>
                    </div>
               </div>
               <div class="same_div">
                    <div class="name_title">
                         <div class="step_num num_family">1</div>
                         <div class="name_title_name_time">
                              <h4>{{ afterInfo.receiver_name }}</h4>
                              <span>{{ firstTime }}</span>
                         </div>
                    </div>
                    <div class="content_after">
                         <p>售后类型:换货</p>
                         <p>换货原因:{{ afterInfo.reason }}</p>
                         <p v-if="afterInfo.content">问题描述:{{ afterInfo.content }}</p>
                    </div>
               </div>
          </template>
     </div>
</template>

<script>
     import HeaderPage from "../../components/HeaderPage"

     export default {
          name: "AfterHistory",
          data() {
               return {
                    afterInfo: null,
                    userInfo:null
               }
          },
          methods: {
               //     获取售后信息
               getInfo() {
                    var obj = {
                         refund_id: this.$route.query.refund_id
                    }
                    this.postObj(obj, window.PATHS.aftersale.SHOPSREFUND_GETORDERINFO, (result) => {
                         if (result.code == 1) {
                              this.afterInfo = result.data
                              this.userInfo = result.user
                              console.log(this.afterInfo)
                         }
                    })
               },
          },
          mounted() {
               this.getInfo()
          },
          computed: {
               //申请时间转换
               firstTime() {
                    var date = new Date(this.afterInfo.create_time * 1000)
                    var Y = date.getFullYear() + '-';
                    var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                    var D = date.getDate() + ' ';
                    var h = date.getHours() + ':';
                    var m = date.getMinutes() + ':';
                    var s = date.getSeconds();
                    return Y + M + D + h + m + s;
                    // var value = this.afterInfo.create_time
                    //      var min = Math.floor(value%3600);
                    //      var time = Math.floor(value/3600) + ":" + (Math.floor(min/60) < 10? '0'+Math.floor(min/60):Math.floor(min/60)) + ":"+ (value % 60 < 10? '0' + value % 60 : value % 60);
                    //      return time;
               }
          },

          components: {
               HeaderPage
          }
     }
</script>



