<template>
     <!--花呗分期组件-->
     <div class="installment-content">
          <div class="conten-hidden">
               <span style="font-size: 1.4rem">花呗分期(可选)</span>
               <div>
                    <ul>
                         <li>
                              <span>分3期(含手续费)</span><br/>
                              <span>￥{{ threePrice }}/期</span>
                         </li>
                         <li>
                              <span>分6期(含手续费)</span><br/>
                              <span>￥{{ sixPrice }}/期</span>
                         </li>
                         <li>
                              <span>分12期(含手续费)</span><br/>
                              <span>￥{{ twelevePrice }}/期</span>
                         </li>
                    </ul>
               </div>
          </div>
     </div>
</template>

<script>
     export default {
          name: "InstallmentPage",
          //从父组件传过来的sku价钱
          props:['installment'],
          data(){
               return{
                    threePrice:0,
                    sixPrice:0,
                    twelevePrice:0
               }
          },
          methods:{
               calculate(){
                    //第三期
                    this.threePrice = this.stagePrice( this.installment,3,2.3)
                    //第六期
                    this.sixPrice = this.stagePrice(this.installment,6,4.5)
                    //第12期
                    this.twelevePrice = this.stagePrice(this.installment,12,7.5)
               }
          },
          mounted() {
               this.calculate()

          },
          watch:{
               installment(newVal,oldVal){
                    this.installment = newVal
                    this.calculate()
               }
          }

     }
</script>

<style scoped>
     .conten-hidden ul li{
          width: 14rem;
          height: 4.6rem;
          text-align: center;
          padding: 0.7rem 0;
          background-color: #d9d9d9;
          border-radius: 1rem;
          font-size: 1.3rem;
          float: left;
          margin-right: 1rem;
          color: #888888;
     }
     .conten-hidden > div{
          height: 4.6rem;

          margin-top: 1rem;
          width: 100%;
          overflow: scroll;
     }
     .conten-hidden ul{
          height: 100%;
          overflow: hidden;
          width: 45rem;
     }
.conten-hidden{
     width: 100%;
     height: 9.4rem;
     background-color: white;
     padding-top: 1.6rem;
     padding-left: 1.2rem;
     padding-bottom: 1rem;
     margin-top: 1rem;
}
</style>



