<script>
     import InstallmentPage from '../components/InstallmentPage'
     import SkuChoose from '../components/SkuChoose'
     import crypto from "crypto-js"
import storage from '@/common/js/storage.js'
     export default {
          name: "DetailsPage",
          data() {
               return {
                    refreshHeight:100,
                    refresh:false,
                    slider_show:false,
                    showActive:false,
                    showbao:false,
                    current: 0,
                    blackDiv: false,
                    skuMale: false,
                    skuChirebool:false,
                    joinTeamMale: false,
                    headerText: ['宝贝', '评价', '详情'],
                    babyTable:["宝贝详情","购买须知"],
                    headerBool: false,
                    comInfo: null,
                    //商品sku列表
                    skuList: [],
                    allImgNum: 4,
                    chooseImg: '',
                    skuBool: false,
                    installment: {},
                    xinValue: 4,
                    newSku: [],
                    collect:true,
                    drawNum:1,
                    draw_price:null,
                    drawShow:false,
                    message:null,
                    userInfo:null,
                    showTeam:false,
                    teamList:null,
                    TeamMale:false,
                    teamBoosInfo:null,
                    addressList:null,
                    scrollTop:0,
                    babyInfoChoose:true,
                    babyTableInfo:0,
                    shopGoodsList:[],
                    headerheight:0,
                    groupList:[],
                    grounpNum:0,
                    groupEntInfo:null,
                    padBotm:0,
                    skeleton:true,
                    introduceBool:false,
                    //拼团滚动的倒计时
                    detailtimeOver:"",
               //     我发起团后底部倒计时
                    myGroupTime:'',
                    time:"",
                    teamTime:'',
               //     限时抢购计时器
                    seckillTime:"",
                    userVipInfo:null

               }
          },
          methods: {
               //刷新
               onRefresh(){
                    clearInterval(this.detailtimeOver)
                    clearInterval(this.myGroup)
                    this.groupList = []
                    this.shopGoodsList = []
                    this.grounpNum = 0
                    this.getComInfo()
               },
               // 店铺关注
               likeShop(){
                    var obj = {
                         shop_id:this.comInfo.shop_id
                    }
                    this.postObj(obj,window.PATHS.SHOPS_STAR,(result) => {
                         if(result.code == 1){
                              if(this.comInfo.shop.is_star == 1){
                                   this.comInfo.shop.is_star = 0
                              }else{
                                   this.comInfo.shop.is_star = 1
                              }
                         }else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               //接收子组件传递的false
               getSkuBoll(data){
                    this.skuMale = data
               },
               //获取店铺4个商品
               getShopInfo(){
                    var obj = {
                         goods_type:'goods',
                         shop_id:this.comInfo.shop_id,
                         page: 1
                    }
                    this.postObj(obj,window.PATHS.SHOPS_GOODS_GET_LIST,(result) => {
                         if (result.code == 1){
                              result.data.forEach((item,index) => {
                                   if (index < 4){
                                        this.shopGoodsList.push(item)
                                   }
                              })
                         } else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               //普通非原价购买
               buySelf(){
                    if (this.comInfo.extra_table == 'goods' || this.comInfo.extra_table == 'goods_customs' || this.comInfo.extra_table == 'goods_exchange' || this.comInfo.extra_table == 'goods_seckill' || this.comInfo.extra_table == 'goods_group'){
                         this.comInfo.is_original = 0
                         this.$store.commit("setIsCarOrBuy",true)
                         this.$store.commit("setSkuInfo",this.comInfo)
                    }
                    this.skuMale = true
               },
               //点击宝贝详情与购买须知
               chageBabyinfo(index){
                    var that = this
                    var width = this.$refs.babyTitleDiv.clientWidth / 2
                    $(".bgRed").animate({
                         left:width * index
                    },300)
                    this.babyTableInfo = index
                    setTimeout(() => {
                         if (index == 0){
                              that.babyInfoChoose = true
                         }
                         if (index== 1) {
                              that.babyInfoChoose = false
                         }
                    },150)
               },
               //显示选择sku底部弹窗
               showSkuAlert(){
                    this.$store.commit("setIsCarOrBuy",false)
                    this.comInfo.is_original = 0
                    this.$store.commit("setSkuInfo",this.comInfo)
                    this.skuMale = true
               },
               //获取地址列表
               getAddList(){
                    this.postObj({},window.PATHS.ADDRESS_GET_LIST,(data) => {
                         if (data.code == 1){
                              if (this.$store.state.address != null){
                                   this.addressList = this.$store.state.address
                                   console.log(this.addressList)
                              } else{
                                   this.addressList = data.data[0]
                              }
                         }
                    })
               },
               //到地址页面
               toPlace(){
                    location.href = "#/place"
               },
               //返回上一页
               goBack(){
                    if (this.$route.query.is_user){
                         location.href = window.EQUITMENTBACK
                    } else{
                         window.history.go(-1)
                    }
               },
               //到拼单历史
               toTeamHistory(aid){
                    this.$router.push({name:"teamhistory",query:{aid}})
               },
               //到图片查看页面
               toLookImg(imgArr,imgIndex){
                    console.log(imgIndex)
                    this.pictureLook(imgArr,imgIndex)
                    // this.preview(imgIndex,imgArr)
               },
               //获取分享过来的拼团信息
               getShareGroup(){
                    var obj = {
                         period_id:this.$route.query.period_id
                    }
                    this.postObj(obj,window.PATHS.GROUP_GETLIST,(result) => {
                         if (result.code == 1){
                              this.teamBoosInfo = result.data
                              this.TeamMale = true
                              this.blackDiv = true
                         } else{
                              this.$toast.center(result.info)
                         }
                    })
               },
               //拼团分享
               shareTeam(){
                    var obj = {
                         title:this.comInfo.title,
                         content:this.comInfo.advertising_title,
                         url:window.HOST + "/goods/group?uid=" + this.userInfo.id + "&goods_id=" + this.comInfo.aid + "&period_id=" + this.comInfo.extra_data.myPeriod.period_id,
                         imgsrc:this.comInfo.thumb,
                         nickname:this.userInfo.nickname,
                         shop_price:this.comInfo.shop_price,
                         market_price:this.comInfo.market_price,
                         sales:this.comInfo.sales,
                         needPersor:this.comInfo.extra_data.number - this.comInfo.extra_data.current,
                         duration:this.comInfo.extra_data.duration,
                         headimgurl:this.userInfo.headimgurl,
                         extra_type:this.comInfo.extra_table
                    }
                    this.shareSame(obj)
               },
               //将秒转换成时分秒
               changeTime(value){
                    var min = Math.floor(value%3600);
                    var time = Math.floor(value/3600) + ":" + (Math.floor(min/60) < 10? '0'+Math.floor(min/60):Math.floor(min/60)) + ":"+ (value % 60 < 10? '0' + value % 60 : value % 60);
                    return time;
               },
               //拼团弹出框倒计时
               alerTime(teamList){
                    this.teamTime = setInterval(() => {
                         for (var i = 0;i < teamList.length;i++){
                              this.teamList[i].duration --
                              this.teamList[i].time = this.changeTime(this.teamList[i].duration)
                         }
                    },1000)
               },
               //查看更多拼团的弹窗
               findMoreTeam(){
                    this.showTeam = true
                    this.$loading("加载中...")
                    var obj = {
                         goods_id:this.comInfo.aid,
                         page:1,
                         is_end:0
                    }
                    this.postObj(obj,window.PATHS.GROUP_GETPERIODLIST,(result) => {
                         if (result.code == 1){
                              console.log(result)
                              this.$loading.close()
                              this.teamList = result.data
                              this.alerTime(this.teamList)
                         } else {
                              this.$toast.center(result.info)
                         }
                    })
               },
               //分享普通商品
               shareCom(){
                    var obj = {
                         title:this.comInfo.title,
                         content:this.comInfo.advertising_title,
                         url:window.HOST + "/goods/detail?uid=" + this.userInfo.id + "&gid=" + this.comInfo.aid,
                         imgsrc:this.comInfo.thumb,
                         price: this.comInfo.shop_price,
                         sharePrice:this.comInfo.share_price
                    }
                    this.shareSame(obj)
               },
               //到客服页面
               toMessage(){
                    var obj = {
                         goods_id:this.comInfo.aid,
                         title:this.comInfo.title,
                         thumb:this.comInfo.thumb,
                         goods_price:this.comInfo.shop_price,
                         shopname:this.comInfo.shop.name,
                         shopthumb:this.comInfo.shop.thumb
                    }
                    if (this.newSku.length == this.comInfo.skugroup.length){
                         var skuStar = this.newSku.join(",")
                         obj.sku = skuStar
                         this.comInfo.skulist.forEach((item,index) => {
                              if (item.title == skuStar) {
                                   obj.sku_path = item.path
                              }
                         })
                    }else {
                         obj.sku = ""
                         obj.sku_path = ""
                    }
                    obj = JSON.stringify(obj)
                    obj = this.getBaseStr(obj)
                    location.href = "app://message/"+ this.comInfo.shop_id + "/" + obj
               },
               //到评价列表页面
               toCommentList(){
                    this.$router.push({name:"commentlist",query:{goods_id:this.comInfo.aid}})
               },
               //到店铺页面
               toShop(){
                    this.$router.push({name:"shop",query:{shop_id:this.comInfo.shop_id}})
                    // this.$router.push({name:"allshop",query:{shop_id:this.comInfo.shop_id}})
               },
               //点击使用钻石支付
               payDraw(){
                    var obj = {
                         goods_id:this.comInfo.aid,
                         number:this.drawNum,
                         period_id:this.comInfo.extra_data.period_id
                    }
                    console.log(obj)
                    this.postObj(obj,window.PATHS.SHOPS_ORDER_DUOBAO_ORDER,(result) => {
                         if (result.code == 1){
                              this.$toast.center(result.info)
                              this.drawShow = false
                              this.blackDiv = false
                              this.getComInfo()
                         } else{
                              this.$toast.center(result.info)
                              this.drawShow = false
                              this.blackDiv = false
                              this.drawNum = 1
                              this.getComInfo()
                         }
                    })
               },
               //点击抽奖
               toDrawEnt(){
                    this.drawShow = true
               },
               //抽奖底部减
               drawJianNum(){
                    if (this.drawNum <= 1) {
                         return
                    }
                    this.drawNum--
                    this.draw_price = (this.comInfo.shop_price * this.drawNum * 100 / 100).toFixed(2)
               },
               //抽奖底部加
               drawAddNum(){
                    if (this.drawNum >= this.comInfo.extra_data.number - this.comInfo.extra_data.current) {
                         return
                    }
                    this.drawNum++
                    this.draw_price = (this.comInfo.shop_price * this.drawNum * 100 / 100).toFixed(2)
               },
               //参与拼团
               joinTeam(period_id){
                    this.comInfo.is_original = 0
                    this.skuMale = true
                    this.joinTeamMale = false
                    this.blackDiv = false
                    if (period_id){
                         this.comInfo.period_id = period_id
                    }else{
                         this.comInfo.period_id = this.$route.query.period_id
                    }
                    this.$store.commit("setIsCarOrBuy",true)
                    this.$store.commit("setSkuInfo",this.comInfo)
               },
               //去拼单
               goTeam(item){
                    console.log(item)
                    this.$store.commit("setIsCarOrBuy",true)
                    // 设置加入团确认弹窗的信息
                    this.groupEntInfo = item
                    this.showTeam = false
                    this.blackDiv = true
                    this.joinTeamMale=true
               },
               //点击发起拼团
               actionTeam(){
                    this.comInfo.is_original = 0
                    this.$store.commit("setIsCarOrBuy",true)
                    this.$store.commit("setSkuInfo",this.comInfo)
                    this.skuMale = true
               },
               //点击原价购买
               original(){
                    this.comInfo.is_original = 1
                    this.$store.commit("setIsCarOrBuy",true)
                    this.$store.commit("setSkuInfo",this.comInfo)
                    this.skuMale = true
               },
               //转换拼团倒计时
               getTime(time){
                    var min=Math.floor(time%3600);
                    return Math.floor(time/3600) + "时" + Math.floor(min/60) + "分"+ time%60 + "秒";
               },
               //收藏商品
               collectCom(){
                    this.postObj({goods_id:this.comInfo.aid},window.PATHS.SHOPS_GOODS_COLLECT,(result) => {
                         if (result.code == 1){
                              if (this.collect) {
                                   this.$toast.center('已添加至收藏')
                                   this.collect = false
                              }else{
                                   this.collect = true
                              }

                         } else {
                          this.$toast.center(result.info)
                         }
                    })
               },
               shoppingCarUrl() {
                    location.href = "#/shoppingcar"
               },
               //接收子组件传递的价钱
               shouPrice(data) {
                    this.comInfo.shop_price = data
               },
               //接收子组件传递的sku
               showSku(data) {
                    this.newSku = data
               },
               onChange(index) {
                    this.current = index;
               },
               shouMesFromSku(data) {
                    this.skuMale = data
                    this.blackDiv = data
                    this.joinTeamMale = data
               },
               toaskevery() {
                    this.$router.push({name:"aksevery",query:{goods_id:this.comInfo.aid}})
               },
               toShoppingCar() {
                    this.skuMale = true;
                    this.blackDiv = true;
               },
               //点击平滑滑动
               scrollTo(index) {
                    if (this.$refs.header) {
                         var HeaderHeight = this.$refs.header.clientHeight;
                    }
                    if (index == 0){
                         $(this.$refs.detailsContent).animate({scrollTop:0}, 200);
                    }else{
                         var setTop = $('.AllScroll').eq(index)[0].offsetTop - HeaderHeight
                         $(this.$refs.detailsContent).animate({scrollTop:setTop}, 200);
                    }

               },
               windowScroll() {
                    this.scrollTop = this.$refs.detailsContent.scrollTop
                    if (this.scrollTop <= 0){
                         this.$refs.detailsContent.scrollTop = 0
                    }
                    if (this.$refs.header) {
                         var HeaderHeight = this.$refs.header.clientHeight
                         this.headerheight = this.$refs.header.clientHeight - 2
                    }

                    var height = this.$refs.detailsContent.scrollTop;
                    //侧边返回顶部和收藏图标
                    this.slider_show = height >= window.innerHeight? true : false
                    if (height >= 44) {
                         this.headerBool = true
                    } else {
                         this.headerBool = false
                    }
                    var allScrollList = $('.AllScroll')
                    for (var i = allScrollList.length - 1; i >= 0; i--) {
                         if (height > allScrollList[i].offsetTop - HeaderHeight - 300) {
                              $('.header ul li').eq(i).addClass('activeHeaderLi').siblings().removeClass('activeHeaderLi')
                              break
                         }
                    }

               },
               toDrawPage() {
                    location.href = '#/drawpeople'
               },
               toWinDrawList() {
                    this.$router.push({name:"drawhistort",query:{goods_id:this.comInfo.aid}})
               },
               // 获取普通商品详情
               getComInfo(){
                    var that = this
                    if (this.$route.query.goods_id){
                         var obj1 = {goods_id: this.$route.query.goods_id};
                    } else{
                         var obj1 = {goods_id: this.$route.query.comId};
                    }
                    this.postObj(obj1, window.PATHS.SHOPS_GOODS_GET_INFO, (result) => {
                         if (result.code == 1000){
                              this.$toast.center("商品已下架")
                              setTimeout(() => {
                                   window.history.go(-1)
                              },1000)
                              return
                         }
                         if(result.data.extra_table == 'goods_seckill'){
                              if (result.data.extra_data.duration != 0){
                                   result.data.extra_data.timeOver = '00:00:00'
                              }

                         }
                         this.userInfo = result.user
                         this.comInfo = result.data
                         //push商品介绍
                         var obj1 = {
                              name:"编号",
                              value:this.comInfo.aid
                         }
                         this.comInfo.params.push(obj1)
                         if(this.comInfo.goods_sn){
                              var obj2 = {
                                   name:"货号",
                                   value:this.comInfo.goods_sn
                              }
                              this.comInfo.params.push(obj2)
                         }
                         console.log(this.comInfo)
                         this.draw_price = this.comInfo.shop_price
                          if(this.comInfo.extra_table == 'goods_group' && this.comInfo.extra_data.newPeriod.length != 0){
                              do{
                                    this.groupList.push(this.comInfo.extra_data.newPeriod.splice(0,2))
                                    if(this.comInfo.extra_data.newPeriod.length == 0){
                                         break
                                    }
                              }while(true)
                              this.groupList.forEach((item) => {
                                   item.forEach(() => {
                                        this.grounpNum++
                                   })
                              })
                         }
                         //如果是通过视频带货过来的
                         if (this.$route.query.video_id){
                              this.comInfo.video_id = this.$route.query.video_id
                         }

                         //设置总图片数量
                         this.allImgNum = result.data.atlas.length
                         this.comInfo.is_star == 0? this.collect = true : this.collect = false
                         if (this.comInfo.extra_data.myPeriod && this.$route.query.period_id) {
                              this.$toast.center("您正在拼团，无法加入")
                         }
                         else if(this.$route.query.period_id){
                              this.getShareGroup()
                         }
                         this.getShopInfo()
                         // 当前商品有的团的倒计时
                         if(this.comInfo.extra_table == 'goods_group' &&  this.groupList.length != 0){
                              this.detailtimeOver = setInterval(() => {
                                   that.groupList.forEach((item) => {
                                        item.forEach((item,index) => {
                                             item.duration--
                                             item.time = that.getTime(item.duration)
                                        })
                                   })
                              },1000)
                         }
                         // 当我自己有团的倒计时
                         if(this.comInfo.extra_table == 'goods_group' &&  this.comInfo.extra_data.myPeriod != null){
                              this.myGroup = setInterval(() => {
                                   that.comInfo.extra_data.myPeriod.duration--
                                   that.comInfo.extra_data.myPeriod.time = that.changeTime(that.comInfo.extra_data.myPeriod.duration)
                              },1000)
                         }
                         this.skeleton = false
                         setTimeout(() => {
                              this.refresh = false
                         },500)
                    //     当是秒杀显示倒计时
                         if(this.comInfo.extra_table == 'goods_seckill'){
                              if (this.comInfo.extra_data.duration != 0){
                                   this.seckillTime = setInterval(() => {
                                        this.comInfo.extra_data.duration--
                                        this.comInfo.extra_data.timeOver = this.changeDay(this.comInfo.extra_data.duration)
                                   },1000)
                              }

                         }
                    })
               },
          },
          mounted() {
               var that = this
               if (this.time) {
                    clearInterval(this.time)
               }
               this.time = setInterval(function () {
                    var arr = that.$store.state.allMessageList.shift()
                    if (arr) {
                         $('.scroll-message').fadeOut(500)
                         setTimeout(function () {
                              that.message =  arr
                              $('.scroll-message').fadeIn(500)
                         },200)
                    }else{
                         $('.scroll-message').fadeOut(500)
                    }
               },5000)
          },
          created() {
               if(storage.get("userVipInfo")){
                    this.userVipInfo = storage.get("userVipInfo")
               }
               this.getComInfo()
               this.getAddList()
          },
          computed:{
               // 筛选显示的sku
               skuSlit(){
                    return this.comInfo.skulist.filter((item,index) => {
                         return item.icon_id != '' && index < 5
                    })
               },
          },
          watch: {
               skuMale(newVal,oldVal){
                    if (!newVal){
                         var that = this
                         setTimeout(() => {
                              that.skuChirebool = false
                         },500)
                    } else{
                         this.skuChirebool = true
                    }
               },
               comInfo(newVal, oldVal) {
                    this.skuBool = true
               },
               showTeam(newVal,oldVal){
                    if (!oldVal){
                         clearInterval(this.teamTime)
                    }
               },
          },
          activated(){
               if (this.$refs.detailsContent){
                    this.$refs.detailsContent.scrollTop = this.scrollTop
               }
               if (this.$route.meta.keepLoadBool == true){
                    this.grounpNum = 0
                    this.groupList = []
                    this.drawNum = 1
                    this.shopGoodsList = []
                    clearInterval(this.detailtimeOver)
                    this.getComInfo()
               }
          },
          beforeRouteLeave(to,from,next){
               this.joinTeamMale = false
               if (to.name == '/' || to.name == "teampay" || to.name == "draw" || to.name == "searcjresult" || to.name == 'zero' || to.name =='seckill' || to.name == 'mustdetail' || to.name == 'shoppingcar' || to.name == 'hothome' || to.name == 'mycomment' || to.name == "footprint" || to.name == 'mustdetail' || to.name == 'commentindex' || to.name == "mustbuygoods" || to.name == "freshindex" || to.name == "recommendindex" || to.name == 'videoshop'){
                    this.comInfo = null
                    this.newSku = []
                    this.skeleton = true
                    this.scrollTop = 0
                    this.headerBool = false
                    this.introduceBool = false
                    this.babyInfoChoose = true
                    this.current = 0
                    clearInterval(this.teamTime)
                    clearInterval(this.detailtimeOver)
                    clearInterval(this.myGroup)
                    clearInterval(this.seckillTime)
                    from.meta.keepLoadBool = true
               }else{
                    from.meta.keepLoadBool = false
               }
               next()
          },
          beforeRouteEnter(to,from,next){
               if (from.name == 'order') {
                    clearInterval(this.teamTime)
                    clearInterval(this.detailtimeOver)
                    clearInterval(this.myGroup)
                    to.meta.keepLoadBool = true
                    next((vm) => {
                         vm.comInfo = null
                    })
               }
               next()

          },
          components: {
               InstallmentPage,
               SkuChoose
          },
     }
</script>





