<style scoped lang="scss">
     #after_list{
          height: 100vh;
          background-color: #f6f6f6;
          overflow-y: scroll;
     }
     .shop_detail_info{
          padding: 14px 12px;
          background-color: #ffffff;
          border-radius: 7px;
          margin-top: 7px;
     }
     .shop_name{
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 1.5rem;
          >div{
               display: flex;
               align-items: center;
          }
     }
     .goods_info{
          display: flex;
          align-items: flex-start;
          justify-content: flex-start;
          font-size: 1.5rem;
          margin-top: 15px;
          height: 10rem;
          .goods_thumb{
               width: 10rem;
               height: 100%;
               border-radius: 5px;
               background-color: #9d9d9d;
               background-size: cover;
               flex-shrink: 0;
          }
          .goods_title{
               margin-left: 10px;
               display: flex;
               flex-direction: column;
               justify-content: space-between;
               height: 100%;
               p{
                    text-overflow: -o-ellipsis-lastline;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    line-clamp: 2;
                    -webkit-box-orient: vertical;
               }
               span{
                    display: inline-block;
                    color: #9d9d9d;
               }
          }
     }
     .shop_anme_left{
          .shop_img{
               width: 2.5rem;
               height: 2.5rem;
               border-radius: 50%;
               background-size: cover;
          }
          span{
               margin-left: 10px;
          }
     }
     .mess_shop{
          img{
               width: 1.2rem;
               height: 1.1rem;
               margin-right: 10px;
          }
          span{
               font-size: 1.3rem;
          }
     }
     .list{
          padding: 0 12px;
     }
     .after_waring{
          margin-top: 15px;
          padding: 8px 12px;
          background-color: #f6f6f6;
          font-size: 1.3rem;
          border-radius: 5px;
     }
     .fing_detail{
          text-align: right;
          margin-top: 10px;
          span{
               display: inline-block;
               line-height: 2.9rem;
               width: 9rem;
               border-radius: 1.45rem;
               border: solid 1px #9d9d9d;
               color: #9d9d9d;
               text-align: center;
               font-size: 1.3rem;
          }
     }
     .afterlist_header{
          background-color: #ffffff;
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          z-index: 20;
     }
     .more{
          text-align: center;
          font-size: 1.3rem;
          padding: 10px;
     }
</style>
<template>
     <!--售后详情-->
     <div ref="afterList" id="after_list" class="steep_padding" @scroll="scroll">
          <div class="afterlist_header">
               <HeaderPage>
                    <span slot="headerTitle">退货/售后</span>
               </HeaderPage>
          </div>
          <van-pull-refresh style="padding-top: 4.4rem;height: 100%;" v-model="isLoading" @refresh="onRefresh">
               <div class="list">
                    <ul>
                         <li v-for="(item,index) in afterList" :key="index + 'afterins'">
                              <div class="shop_detail_info">
                                   <div class="shop_name">
                                        <div class="shop_anme_left">
                                             <div class="shop_img" :style="{backgroundImage:'url('+ item.shop_thumb +')'}"></div>
                                             <span>店铺名称</span>
                                             <img src="@/common/image/publicImg/mail_ico_point_v.png" alt="" style="width: 0.55rem;height: 1rem;margin-left: 9px">
                                        </div>
                                        <div class="mess_shop">
                                             <!--仅退款-->
                                             <template v-if="item.refund_type == 1">
                                                  <img src="@/common/image/aftersale/Rectangle-path@2x.png" alt="">
                                                  <span style="color: #f24367">退款</span>
                                             </template>
                                             <!--退货退款-->
                                             <template v-if="item.refund_type == 2">
                                                  <img src="@/common/image/aftersale/huanhuo@2x.png" alt="">
                                                  <span style="color: #f3ab64">退货退款</span>
                                             </template>
                                             <!--换货-->
                                             <template v-if="item.refund_type == 3">
                                                  <img src="@/common/image/aftersale/huanhuo@2x (2).png" alt="">
                                                  <span style="color: #f24367">换货</span>
                                             </template>
                                        </div>
                                   </div>
                                   <div class="goods_info">
                                        <div class="goods_thumb" :style="{backgroundImage: 'url('+ item.thumb +')'}"></div>
                                        <div class="goods_title">
                                             <p>{{ item.title }}</p>
                                             <span style="font-size: 1.3rem">{{ item.specs }}</span>
                                             <span v-if="item.refund_type == '1' || item.refund_type == '2'">退款:￥{{ item.refund_price }}元</span>
                                        </div>
                                   </div>
                                   <div class="after_waring">
                                        <!--退款-->
                                        <template v-if="item.refund_type == 1">
                                             <span v-if="item.status == '0'">已提交退款申请</span>
                                             <span v-if="item.status == '5'">该售后已撤销</span>
                                             <span v-if="item.status == '6'">商家已同意申请，留意退款到账</span>
                                             <span v-if="item.status == '2'">商家拒绝退款</span>
                                        </template>
                                        <!--退货退款-->
                                        <template v-if="item.refund_type == 2">
                                             <span v-if="item.status == '0'">已提交退货退款申请</span>
                                             <span v-if="item.status == '1'">商家已同意申请，请填写物流信息</span>
                                             <span v-if="item.status == '2'">商家拒绝退款退货</span>
                                             <span v-if="item.status == '3'">您已经发出货物等待商家确认</span>
                                             <span v-if="item.status == '5'">该售后已撤销</span>
                                             <span v-if="item.status == '6'">商家已收到货物，留意退款到账</span>
                                        </template>
                                        <!--换货-->
                                        <template v-if="item.refund_type == 3">
                                             <span v-if="item.status == '0'">已提交换货申请</span>
                                             <span v-if="item.status == '1'">商家已同意换货申请，请填写您的发货信息</span>
                                             <span v-if="item.status == '2'">商家拒绝退款退货</span>
                                             <span v-if="item.status == '3'">等待商家确认收到货物</span>
                                             <span v-if="item.status == '5'">该售后已撤销</span>
                                             <span v-if="item.status == '6'">商家已收到货物，等待新的商品到手</span>
                                        </template>
                                   </div>
                                   <div class="fing_detail">
                                        <span v-if="item.status == 0 || item.status == 1 || item.status == 3" @click="closeShen(item.aid,index)">撤销申请</span>
                                        <span @click="toAfterDetail(item)" style="margin-left: 10px;">查看详情</span>
                                   </div>
                              </div>
                         </li>
                    </ul>
               </div>
               <h3 class="more" v-if="!moreBool">已加载全部</h3>
          </van-pull-refresh>
     </div>
</template>

<script>
     import { Dialog } from 'vant';
     import HeaderPage from "../../components/HeaderPage"
     export default {
          name: "AfterDetail",
          data(){
               return{
                    afterList:[],
                    moreBool:true,
                    page:1,
                    scrollTop:0,
                    isLoading:true
               }
          },
          methods:{
               //撤销申请
               closeShen(aid,index){
                    Dialog.confirm({
                         title: '撤销售后',
                         message: '确定要撤销该售后吗？'
                    }).then(() => {
                         var obj = {
                              refund_id:aid
                         }
                         this.postObj(obj,window.PATHS.aftersale.SHOPSREFUND_REVOKEORDER,(result) => {
                              if (result.code == 1){
                                   this.$toast.center("撤销成功")
                                   this.afterList[index].status = 5
                              }
                         })
                    }).catch(() => {
                         // on cancel
                    });
               },
               //刷新
               onRefresh() {
                    var that = this
                    this.isLoading = true
                    this.page = 1
                    setTimeout(() => {
                         that.$toast('刷新成功');
                         that.isLoading = false;
                         that.getAfterDetail((data) => {
                              that.afterList = data
                              console.log(that.afterList)
                         })
                    }, 1000);
               },
               //到售后详情
               toAfterDetail(val){
                    this.$router.push({name:"afterdetail",query:{refund_id:val.aid}})
               },
               getAfterDetail(backfun){
                    var that = this
                    if (this.$route.query.isyuan){
                         var obj = {
                              page:this.page
                         }
                    } else{
                         var obj = {
                              order_no:this.$route.query.order_no,
                              order_goods_id:this.$route.query.order_goods_id,
                              page:this.page
                         }
                    }
                    this.postObj(obj,window.PATHS.aftersale.SHOPSREFUND_GETORDERLIST,(result) => {
                         if (result.code == 1){
                              if (result.data.length != 0){
                                   if (backfun){
                                        backfun(result.data)
                                   }
                                   this.afterList = this.afterList.concat(result.data)
                                   setTimeout(() => {
                                        that.moreBool = true
                                   },1000)
                              }else{
                                   setTimeout(() => {
                                        that.moreBool = false
                                   },1000)
                              }
                         }
                    })
               },
               scroll(){

                    this.scrollTop = this.$refs.afterList.scrollTop
                    var height = this.$refs.afterList.scrollHeight
                    var scrollTop = this.$refs.afterList.scrollTop
                    if (scrollTop <= 1){
                         this.$refs.afterList.scrollTop = 0
                    }
                    var windoHeight = window.innerHeight * 2
                    this.scrollMore(scrollTop,height,window.innerHeight,() => {
                         if (this.moreBool){
                              this.moreBool = false
                              this.page++
                              this.getAfterDetail()
                         }
                    })
               },
               //初始化数据
               initialize(){
                    this.afterList = []
                    this.moreBool = true
                    this.page = 1
                    this.scrollTop = 0
               },
          },
          created() {
               this.getAfterDetail()
          },
          activated(){
               if (this.$refs.afterList) {
                    this.$refs.afterList.scrollTop = this.scrollTop
               }
               if (this.$route.meta.keepLoadBool){
                    this.getAfterDetail()
               }
          },
          components:{
               HeaderPage
          },
          beforeRouteLeave(to,from,next){
               if (to.name == "afterdetail"){
                    from.meta.keepLoadBool = false
               }else{
                    this.initialize()
                    from.meta.keepLoadBool = true
               }
               next()
          }
     }
</script>





